@charset "utf-8";

/* Style */
html {
    height: 100%;

    body {
        height: 100%;
        //min-width: 320px;

        &:not(.only_doc) {
            &:before {
                content: '';
                position: fixed;
                top: 50%;
                left: 50%;
                width: 60px;
                height: 60px;
                margin: -60px 0 0 -30px;
                border-top: 4px solid #666;
                border-right: 4px solid rgba(0, 0, 0, 0.2);
                border-bottom: 4px solid rgba(0, 0, 0, 0.2);
                border-left: 4px solid rgba(0, 0, 0, 0.2);
                border-radius: 50%;
                background-color: #ffffff;
                animation: loaderAni 1.1s infinite cubic-bezier(0.5, 0, 0.5, 1);
            }
            &:after {
                content: 'Loading...';
                position: fixed;
                top: 50%;
                left: 50%;
                transform: translateX(-50%) translateY(-50%);
                margin: 1em 0 0 0;
                color: #cccccc;
                font-size: 2.667em;
                font-weight: bold;
            }

            #wrap {
                // visibility: hidden;
                // @include opacity(0);
                // @include transition(0.3, 0, opacity);
            }
        }

        #wrap {
            display: flex;
            flex-direction: column;
            position: relative;
            min-width: $doc_wid;
            min-height: 100%;
            // visibility: hidden;
            box-sizing: border-box;
            overflow: hidden;

            .appContainerWrap {
                flex: 1 1;
                display: flex;
                flex-direction: column;
                height: 100%;
            }

            header {
                position: relative;
                //z-index: 1;
                @include transition(0.3, 0, filter);
            }

            #container {
                flex: 1 1;
                position: relative;
                @include transition(0.3, 0, filter);
                padding-bottom: 40px;

                /*
				padding: 95px 0 0 0;
				box-sizing: border-box;

				#content {
					position: relative;
					//height: 100%;
					box-sizing: border-box;
				}

				@media ( $mq_tablet ) {					// Tablet
					padding: 0 !important;
                }
                */

                @media ($mq_mobile) {
                    // Mobile
                    padding: 54px 0 0 0;
                }
            }

            header:not(.is-fixed) + #container #content > .wrap > .contents {
                //margin-top: 0 !important;
            }

            footer {
                /*
                position: absolute;
				bottom: 0;
				left: 0;
				min-width: 100%;
				background: #313131;
                color: #777777;
                */
            }

            &.is-blured {
                header {
                    //filter: blur(6px);
                }
                #container {
                    //filter: blur( 6px );
                }
            }
        }

        > .ui_loader {
            position: fixed !important;
            z-index: 990 !important;
        }

        * {
            @media ($mq_mobile) {
                // Mobile
                border-radius: 0 !important;
            }
        }

        &.is-vue-loaded {
            #wrap {
                // height: 100%;
                // visibility: visible;
                overflow: visible;
                // @include opacity(1);
            }

            &:before {
                display: none;
            }
            &:after {
                display: none;
            }
        }
    }
}
