//색변수
:root {
    --pointBgColor: #000;
    --pointTxtColor: #fff;
    --focusBoxOnOff: none;
}

.age_rank_wrap {
    padding-top: 1px;

    .rank_wrap {
        width: 100%;
        padding: 0 50px;
        box-sizing: border-box;
        position: relative;

        .rank_table_wrap {
            width: 100%;
            overflow: hidden;

            .ui_brd>.wrap {
                width: calc((100% - 20px * 3) / 4);
                margin-right: 20px;

                &:nth-child(4n) {
                    margin-right: 0;
                }

                table {
                    thead {
                        th:last-child {
                            cursor: pointer;
                        }
                    }

                    tbody {
                        tr {
                            th {
                                pointer-events: none;
                            }

                            td {
                                cursor: pointer;
                            }

                            &.on {
                                td {
                                    color: #fff;

                                    &:before {
                                        background-color: #666;
                                    }
                                }

                            }
                        }

                    }
                }

                .empty_table {
                    display: flex;
                    height: calc(100% - 36px - 5px);
                    justify-content: center;
                    align-items: center;
                    background: #f7f7f7;
                    border-radius: 5px;
                }

                &>.empty_table:first-child {
                    height: 100%;

                    span {
                        &::before {
                            font-size: 54px;
                            opacity: 0.2;
                        }
                    }
                }

            }
        }
    }

    .btn_box {
        button {
            width: 40px;
            height: 100%;
            border-radius: 10px;
            font-family: 'icomoon';
            font-size: 50px;
            text-indent: -5px;
            transition: all 0.3s;
            position: absolute;
            top: 0;

            &:hover {
                background: rgba(#ddd, 0.3);
            }

            &:disabled {
                color: #ddd;
                cursor: not-allowed;
            }

            &.prev_btn {
                left: 0;
            }

            &.next_btn {
                right: 0;
            }
        }
    }
}