.page-anals {
    .tpop {
        height: 473px;

        > .wrap {
            display: flex;
            position: relative;
            flex-wrap: nowrap;
            align-items: stretch;
            justify-content: space-between;
            gap: 20px;
            overflow-x: auto;

            > .item {
            }

            > hr {
                flex: 0 0;
                height: auto;
                border: none;
                border-right: 1px dashed #e6e6e6;
                background: none;
                box-sizing: border-box;
            }
        }
    }
}
