@charset "utf-8";

/* Style */
#wrap {
    header {
        position: sticky !important;
        top: -85px;
        left: 0;
        width: 100%;
        height: 125px;
        z-index: 8;
        //@include transition( 0.2 );

        > .wrap {
            display: flex;
            flex-direction: column;
            position: relative;
            // top: 0;
            max-width: $doc_max_wid;
            height: 100%;
            margin: 0 auto;
            box-sizing: border-box;
            @include transition(0.2);

            > h1 {
                height: 55px;
                overflow: hidden;
                color: #000;
                font-size: 1.667em;
                text-align: center;
                line-height: 50px;

                > a {
                    @include inlineBlock();
                    padding: 0 20px;
                    line-height: 1;
                    vertical-align: middle;

                    > img {
                        display: block;
                        height: 55px;
                    }

                    @media ($mq_mobile) {
                        // Mobile
                        padding: 3px 10px;

                        img {
                            height: 48px;
                        }
                    }
                }
            }

            > .util_nav_wrap {
                margin-top: auto;

                > div:first-child {
                    position: relative;
                    top: -55px;
                    max-width: $doc_max_wid;
                    margin: 0 auto;
                }

                .util {
                    position: absolute;
                    top: 18px;
                    right: 0;

                    > ul {
                        display: flex;
                        align-items: center;

                        > li {
                            position: relative;
                            padding: 0 2px;

                            // &:after {
                            //     content: '';
                            //     position: absolute;
                            //     top: 50%;
                            //     right: 0;
                            //     width: 1px;
                            //     height: 16px;
                            //     margin: -8px 0 0 0;
                            //     background: #e2e2e2;
                            // }

                            &:first-child {
                                padding-right: 10px;
                            }

                            &:last-child {
                                padding-right: 0;

                                &:after {
                                    content: none;
                                }
                            }

                            > * {
                                display: block;
                                @include transition();
                            }
                        }
                    }
                }

                .gnb {
                    // position: sticky;
                    width: 100%;
                    max-width: 1400px;
                    border-radius: 5px 5px 0 0;
                    background: linear-gradient(135deg, $gr_color_01 0%, $gr_color_02 100%);
                    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr=#{$gr_color_01}, endColorstr=#{$gr_color_02}, GradientType=1 );
                    text-align: center;
                    //@include shadow( 0, -1px, 3px, 0.8, 0, 0, 0 );
                    @include transition(0.2);
                    z-index: 8;

                    > ul {
                        display: flex;
                        justify-content: center;
                        // @include inlineBlock();
                        // @include clearBoth();
                        text-align: left;
                        vertical-align: top;
                        // @include transition(0.2);

                        > li {
                            // float: left;
                            padding: 0;

                            > * {
                                position: relative;

                                > .item {
                                    display: block;
                                    position: relative;
                                    padding: 0 40px;
                                    color: #ffffff;
                                    font-size: 16px;
                                    font-weight: bold;
                                    line-height: 70px;
                                    overflow: hidden;
                                    z-index: 1;
                                    @include transition(0.2);

                                    &:before {
                                        content: '';
                                        position: absolute;
                                        top: 0;
                                        left: 0;
                                        width: 100%;
                                        height: 100%;
                                        background: #000000;
                                        @include opacity(0);
                                        @include transition(0.2);
                                    }

                                    > .txt {
                                        position: relative;
                                    }

                                    .exanpender {
                                        display: none;
                                    }

                                    &.is-active {
                                        &:before {
                                            @include opacity(0.3, !important);
                                        }

                                        &.is-expanded + .sub {
                                            background: #f2f2f2;
                                        }
                                    }
                                }

                                > .sub {
                                    position: absolute;
                                    left: 50%;
                                    transform: translateX(-50%);
                                    min-width: 100%;
                                    border-radius: 0 0 5px 5px;
                                    background: #ffffff !important;
                                    visibility: hidden;
                                    overflow: hidden;
                                    @include opacity(0);
                                    z-index: 0;
                                    @include shadow(0, 3px, 5px, 0.1, 0, 0, 0);
                                    @include transition(0.2);

                                    &:after {
                                        content: '';
                                        display: block;
                                        height: 5px;
                                        background: linear-gradient(135deg, $gr_color_01 0%, $gr_color_02 100%);
                                        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr=#{$gr_color_01}, endColorstr=#{$gr_color_02}, GradientType=1 );
                                    }

                                    > ul {
                                        position: relative;
                                        padding: 0;
                                        //margin: -50px 0 0 0;
                                        @include transition(0.16);

                                        > li {
                                            > .item {
                                                display: block;
                                                padding: 10px 20px;
                                                color: $font_color_dm;
                                                font-size: 1.167em;
                                                line-height: 1.333em;
                                                white-space: nowrap;
                                                @include transition();

                                                &:before {
                                                    content: none;
                                                }

                                                &:hover {
                                                    background: #e1e1e1;
                                                    color: #515151;

                                                    &:before {
                                                        left: -2px;
                                                        @include opacity(1);
                                                    }
                                                }

                                                &.is-active {
                                                    background: $point_color;
                                                    color: #ffffff;
                                                    font-weight: bold;

                                                    &:before {
                                                        left: -2px;
                                                        @include opacity(1);
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            }

                            &:hover {
                                > * {
                                    .item {
                                        &:before {
                                            //transform: scale( 0.9 );
                                            @include opacity(0.15);
                                        }
                                    }

                                    .sub {
                                        visibility: visible;
                                        @include opacity(1);
                                    }
                                }
                            }
                        }
                    }

                    &.is-no-topsearchs {
                        //border-radius: 5px;
                        //@include shadow( 0, 3px, 8px, 0.15, 0, 0, 0 );
                    }
                }

                .pass_mdfy {
                    position: absolute;
                    top: 32px;
                    right: 0;
                    width: 300px;
                    padding: 20px;
                    border-radius: 4px;
                    border: 1px solid $component_brd_color_disabled;
                    background: #ffffff !important;
                    z-index: 10;
                    @include shadow(0, 3px, 5px, 0.1, 0, 0, 0);

                    .arrow {
                        position: absolute;
                        top: -11px;
                        right: 0;

                        &:before {
                            content: '';
                            position: absolute;
                            left: -5px;
                            margin-top: -1px;
                            @include triangle('top', $component_brd_color_disabled, 18px, 12px);
                        }

                        &:after {
                            content: '';
                            position: absolute;
                            left: -5px;
                            @include triangle('top', #ffffff, 18px, 12px);
                        }
                    }

                    > strong {
                        display: block;
                        padding: 0 20px 13px;
                        margin: 0 -20px 20px;
                        border-bottom: 1px solid #eeeeee;
                        color: $font_color_hl;
                        font-size: 1.25em;
                    }

                    > .dcp {
                        width: 100%;
                        margin: 3px 0;
                    }

                    > .result {
                        display: block;
                        padding: 5px 0 0 0;
                        color: #e65f5f;
                        font-size: 0.917em;
                        line-height: 1.4em;

                        &:empty {
                            padding: 0;
                        }

                        &:not(:empty) {
                            &:before {
                                content: '! ';
                            }

                            & + .info {
                                padding: 0;
                            }
                        }
                    }

                    > .info {
                        display: block;
                        padding: 5px 0 0 0;
                        color: $font_color_dm;
                        font-size: 0.917em;
                        line-height: 1.4em;

                        &:before {
                            content: '※ ';
                        }
                    }

                    > .btns {
                        padding: 10px;
                        margin: 10px -20px -20px;
                        border-radius: 0 0 4px 4px;
                        background: $component_bg_color_over;
                        text-align: center;
                    }
                }

                // 모바일
                .btn_gnb {
                    @include customComponent();

                    & + label {
                        display: none;
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 54px;
                        height: 54px;
                        background: linear-gradient(135deg, $gr_color_01 0%, $gr_color_02 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
                        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr=$gr_color_01, endColorstr=$gr_color_02, GradientType=1 ); /* IE6-9 */
                        cursor: pointer;
                        @include transition(0.2);

                        > .line {
                            position: absolute;
                            top: 26px;
                            left: 17px;
                            width: 20px;
                            height: 2px;
                            background: #ffffff;
                            @include transition(0.2);

                            &:before,
                            &:after {
                                content: '';
                                position: absolute;
                                top: 0;
                                left: 0;
                                width: 20px;
                                height: 2px;
                                background: #ffffff;
                                @include transition(0.4);
                            }

                            &:before {
                                margin-top: -9px;
                            }
                            &:after {
                                margin-top: 9px;
                            }
                        }
                    }

                    &:checked + label {
                        > .line {
                            background: rgba(255, 255, 255, 0);
                            @include rotate(180);

                            &:before {
                                margin-top: 0;
                                @include rotate(45);
                            }
                            &:after {
                                margin-top: 0;
                                @include rotate(-45);
                            }
                        }

                        & + .m_gnb {
                            margin-left: 0;
                        }
                    }
                }

                .m_util {
                    display: none;
                    position: fixed;
                    top: 54px;
                    left: 0;
                    width: 100%;
                    padding: 9px 12px;
                    margin-left: -100%;
                    border-top: 1px solid #e2e2e2;
                    border-bottom: 1px solid #e2e2e2;
                    background: #f3f3f3;
                    box-sizing: border-box;
                    @include transition(0.3);

                    > .name {
                        display: block;
                        padding: 0 0 0 20px;
                        line-height: 30px;
                        background: url('data:image/svg+xml;base64, ')
                            no-repeat left center;
                    }

                    > .btns {
                        position: absolute;
                        top: 9px;
                        right: 12px;
                    }
                }

                .m_gnb {
                    display: none;
                    position: fixed;
                    top: 85px;
                    left: 0;
                    width: 100%;
                    min-height: calc(100% - 85px);
                    height: calc(100% - 85px);
                    //max-height: calc( 100% - 103px );
                    padding-bottom: 5px;
                    margin-left: -100%;
                    background: #ffffff;
                    box-sizing: border-box;
                    overflow: auto;
                    @include transition(0.3);

                    &:after {
                        content: '';
                        display: block;
                        width: 100%;
                        height: 5px;
                        background: linear-gradient(135deg, $gr_color_01 0%, $gr_color_02 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
                        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr=$gr_color_01, endColorstr=$gr_color_02, GradientType=1 ); /* IE6-9 */
                    }

                    > ul {
                        > li {
                            border-bottom: 1px solid #c4c4c4;

                            > div {
                                > .item {
                                    display: block;
                                    position: relative;
                                    width: 100%;
                                    padding: 19px 12px;
                                    box-sizing: border-box;
                                    font-weight: bold;
                                    font-size: 16px;
                                    text-align: left;
                                    line-height: 22px;

                                    &.has-sub {
                                        &:before,
                                        &:after {
                                            content: '';
                                            position: absolute !important;
                                            top: 29px !important;
                                            left: auto !important;
                                            right: 15px !important;
                                            width: 14px !important;
                                            height: 2px !important;
                                            background: #808080;
                                            @include transition(0.3);
                                        }

                                        &:after {
                                            top: 23px !important;
                                            right: 21px !important;
                                            width: 2px !important;
                                            height: 14px !important;
                                        }

                                        &.is-expanded {
                                            &:before {
                                                @include rotate(180);
                                            }
                                            &:after {
                                                @include rotate(270);
                                            }
                                        }
                                    }

                                    &.is-active {
                                        background: #808080;
                                        color: #ffffff;

                                        &:before,
                                        &:after {
                                            background: #ffffff;
                                        }
                                    }
                                }
                                > .sub {
                                    height: 0;
                                    overflow: hidden;

                                    > ul {
                                        > li {
                                            border-bottom: 1px solid #dedede;

                                            &:first-child {
                                                border-top: 1px solid #dedede;
                                            }
                                            &:last-child {
                                                border-bottom: none;
                                            }

                                            > .item {
                                                display: block;
                                                padding: 16px 20px 16px 30px;
                                                background: #f3f3f3;
                                                color: $font_color_dm;
                                                font-size: 14px;
                                                line-height: 18px;

                                                &.is-active {
                                                    background: $point_color;
                                                    color: #ffffff;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }

                .btn_search {
                    @include customComponent();

                    & + label {
                        display: none;
                        position: fixed;
                        top: 0;
                        right: 0;
                        width: 54px;
                        height: 54px;
                        background: #ffffff;
                        cursor: pointer;

                        > svg {
                            margin: 17px 0 0 17px;

                            path {
                                @include transition();
                            }
                        }
                    }

                    &.is-active + label {
                        > svg {
                            path {
                                fill: $point_color;
                            }
                        }
                    }
                }
            }
        }

        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 90px;
            border-bottom: 1px solid #efefef;
            background: #ffffff;
            @include transition(0.2);
        }

        &.is-fixed {
            //position: fixed;
            //top: -55px;
            //z-index: 1;

            &:before {
                height: 54px;
            }

            & + #container {
                //padding: 120px 0 0 0;

                .ui_location {
                    max-width: 3000px;
                    // top: 40px;

                    > .wrap {
                        border-left: none;
                        border-right: none;
                        border-radius: 0;
                    }
                }

                .top_searchs {
                    // position: fixed;
                    // margin-top: -85px;
                    // width: 100%;

                    > .wrap {
                        max-width: 3000px;
                        border-left: none;
                        border-right: none;
                        border-radius: 0;
                    }
                }
            }

            > .wrap {
                max-width: 3000px;

                .gnb {
                    top: 0;
                    max-width: 3000px;
                    border-radius: 0 !important;

                    > ul {
                        // padding-top: 30px;

                        > li {
                            > * {
                                > .item {
                                    line-height: 40px;
                                    // font-size: 1.167em;
                                }
                            }
                        }
                    }
                }
            }
        }

        @media ($mq_mobile) {
            // Mobile
            position: fixed !important;
            top: 0 !important;
            height: 54px;
            background: #ffffff;
            z-index: 5;

            > .wrap {
                > h1 {
                    height: 54px;

                    > a {
                        line-height: 54px;
                    }
                }

                .util_nav_wrap {
                    .util,
                    .gnb {
                        display: none;
                    }

                    .btn_gnb {
                        display: block;

                        & + label {
                            display: block;
                        }
                    }

                    .m_util {
                        display: block;
                    }
                    .m_gnb {
                        display: block;
                    }

                    .btn_search {
                        & + label {
                            display: block;
                        }
                    }
                }
            }

            &:before {
                display: none;
            }

            &.is-no-topsearchs {
                input.btn_search {
                    display: none;
                }
            }
        }
    }
}
