@charset "utf-8";

// Excel Process Container
.excel_prc_container {
    display: none;
}

// Content 공통
#content {
    //position: relative;

    > h2 {
        display: none;
    }

    > .wrap {
        > .ui_location {
            & + .top_searchs {
                top: 3px;
            }
        }
        > .top_searchs {
            position: sticky;
            top: -29px;
            // top: 127px;
            margin-top: 0 !important;
            left: 0;
            width: 100%;
            z-index: 6;
            @include transition(0.2, 0, margin);

            > .btn_expander {
                display: block;
                position: relative;
                left: 50%;
                transform: translateX(-50%);
                width: 80px;
                height: 20px;
                margin: -1px 0 0 0;
                border: 1px solid #e2e2e2;
                border-top: none;
                border-radius: 0 0 4px 4px;
                background: #ececec;
                text-align: left;
                text-indent: -9999px;
                cursor: pointer;
                overflow: hidden;
                z-index: 1;
                @include shadow(0, 3px, 3px, 0.15, 0, 0, 0);

                &:before {
                    content: '';
                    position: absolute;
                    top: 20px;
                    left: calc(50% - 6px);
                    @include triangle('top', $component_font_color, 12px, 8px);
                    @include transition(0.3);
                }

                &:after {
                    content: '';
                    position: absolute;
                    top: 5px;
                    left: calc(50% - 6px);
                    @include triangle('bottom', $component_font_color, 12px, 8px);
                    @include transition(0.3);
                }
            }

            > .wrap {
                position: relative;
                max-width: $doc_max_wid;
                padding: 68px 0 0;
                margin: -68px auto 0;
                box-sizing: border-box;
                border: 1px solid #e2e2e2;
                border-top: 1px solid #dcdcdc;
                border-radius: 5px;
                background: #ececec;
                //z-index: 1;
                @include shadow(0, 3px, 8px, 0.15, 0, 0, 0);
                @include transition(0.2);

                > .inner_wrap {
                    overflow: hidden;

                    > .searchs {
                        display: flex;
                        position: relative;
                        max-width: $doc_max_wid;
                        padding: 0 20px;
                        margin: 0 auto;
                        box-sizing: border-box;
                        overflow: hidden;

                        > h3 {
                            padding: 15px 0 8px;
                            font-size: 1.167em;
                            font-weight: normal;
                            line-height: 1;
                        }

                        > .input_form {
                            width: 70%;
                            padding: 20px 0;

                            > .wrap {
                                > tr {
                                    > * {
                                        padding: 3px;
                                        line-height: 30px;
                                        vertical-align: top;
                                    }

                                    > th {
                                        padding-right: 20px;
                                        text-align: left;
                                    }

                                    > table {
                                        float: none;
                                        width: 100%;
                                    }
                                }
                            }
                        }

                        > .input_result {
                            display: flex;
                            flex-direction: column;
                            position: relative;
                            width: 30%;
                            padding: 20px 0 20px 20px;
                            box-sizing: border-box;

                            &:before {
                                content: '';
                                position: absolute;
                                top: 10px;
                                left: 0;
                                width: 0;
                                height: calc(100% - 20px);
                                border-left: 1px solid #e1e1e1;
                            }

                            > .wrap {
                                flex: 1 1;
                                display: flex;
                                flex-direction: column;
                                min-height: 20px;
                                margin: 0 0 10px 0;
                                overflow: auto;
                                color: $font_color_dm;

                                > * {
                                    display: flex;
                                    flex: 0 0;
                                    flex-wrap: nowrap;
                                    position: relative;
                                    // line-height: 1.6em;
                                    padding: 0 0 5px 0;

                                    // &:before {
                                    //     content: '-';
                                    //     position: absolute;
                                    //     top: 0;
                                    //     left: 0;
                                    //     width: 2px;
                                    //     height: 2px;
                                    //     // background: $font_color_dm;
                                    // }

                                    > .title {
                                        flex: 0 0 70px;
                                        color: #666666;
                                        font-weight: normal;
                                    }

                                    > .is-as {
                                        padding: 0px 3px;
                                        border-radius: 4px;
                                        background: #b5b5b5;
                                        color: #ffffff;
                                    }

                                    hr {
                                        width: 1px;
                                        height: 14px;
                                        margin: 2px 5px 0;
                                        border: none;
                                        background: #d5d5d5;
                                    }
                                }
                            }

                            > .btns {
                                display: flex;
                                justify-content: center;
                                margin-top: auto;
                                padding-top: 20px;
                                border-top: 1px solid #e1e1e1;

                                > * {
                                    margin: 0 2px;
                                    font-size: 14px;
                                }

                                .btn_search {
                                    width: 60%;

                                    .txt {
                                        font-size: 16px;
                                    }
                                }
                            }
                        }
                    }

                    > .results {
                        display: block;
                        position: relative;
                        max-width: $doc_max_wid;
                        padding: 5px 20px;
                        margin: 0 auto;
                        box-sizing: border-box;
                        overflow: hidden;
                        line-height: 26px;

                        .item {
                            float: left;
                            min-height: 16px;
                            padding: 5px 15px;
                            margin: 2px;
                            border-radius: 13px;
                            background: rgba(0, 0, 0, 0.55);
                            color: #ffffff;
                            line-height: 16px;

                            &.is-customColor {
                                background: var(--color);
                            }

                            .is-as {
                                @include inlineBlock();
                                padding: 2px 5px;
                                margin: -6px 5px -6px -11px;
                                border-radius: 100px;
                                background: rgba(0, 0, 0, 0.2);
                                color: #ffffff;
                            }
                        }
                    }
                }
            }

            & + .contents {
                //margin-top: -55px;
            }

            &.is-expanded {
                > .btn_expander {
                    &:before {
                        top: 5px;
                    }
                    &:after {
                        top: -15px;
                    }
                }
            }

            @media ($mq_mobile) {
                // Mobile
                top: 82px !important;
                margin-top: 0 !important;

                > .btn_expander {
                    display: none;
                }

                > .wrap {
                    border-left: none;
                    border-right: none;
                    border-radius: 0;

                    > .inner_wrap {
                        > .searchs {
                            position: relative;
                            max-width: $doc_max_wid;
                            margin: 0 auto;
                            overflow: hidden;

                            > h3 {
                                padding: 15px 0 8px;
                                color: $point_color;
                                font-size: 1.167em;
                                line-height: 1;
                            }

                            > .conditions_wrap {
                                @include clearBoth();

                                .condition {
                                    display: block;
                                    float: none;
                                    padding: 5px 0 5px 0;
                                    @include clearBoth();

                                    &.is-wid100p {
                                        padding-right: 0;
                                    }

                                    > * {
                                        float: left;
                                        margin: 2px 5px 2px 0;

                                        &:last-child {
                                            margin-right: 0;
                                        }
                                    }

                                    > h4 {
                                        float: none;
                                        display: block;
                                        padding: 0 0 3px;
                                        margin: 0;
                                        color: $component_font_color;
                                        font-weight: normal;
                                    }

                                    > table {
                                        display: block;

                                        tr {
                                            display: block;

                                            td {
                                                display: block;

                                                &:not(:first-child) {
                                                    padding-top: 2px;
                                                }
                                            }
                                        }
                                    }
                                }
                            }

                            > .ui_btn {
                                display: block;
                                width: 160px;
                                margin: 8px auto 15px;
                                font-size: 1.167em;
                            }
                        }

                        > .results {
                            display: none;
                        }
                    }
                }
            }
        }

        > .contents {
            position: relative;
            // z-index: 0;
            //padding: 30px 0 0 0;
            //@include transition();
            //max-width: 1180px;

            > .wrap {
                padding: 32px 0 0 0;

                @media ($mq_mobile) {
                    // Mobile
                    padding: 30px 0 0 0;
                }
            }

            @media ($mq_mobile) {
                // Mobile
                padding-left: 0 !important;
                padding-right: 0 !important;
                margin-top: -20px !important;
            }
        }
    }
}

// 사이드 영역
#side_follow {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    position: fixed;
    top: 130px;
    right: 0;
    z-index: 7;

    // 도움말
    > .helper {
        width: fit-content;
        padding: 10px 18px;
        border: 1px solid #e6e6e6;
        border-right: none;
        border-radius: 5px 0 0 5px;
        background: #ffffff;
        margin-bottom: 20px;

        > * {
            display: block;
            text-align: center;
        }

        > .title {
            padding-bottom: 5px;
        }
    }

    // 네비게이터
    > .navigator {
        position: relative;
        width: 155px;
        padding: 5px 0;
        border: 1px solid #e6e6e6;
        border-radius: 5px;
        background: #ffffff;
        margin-bottom: 10px;
        color: $font_color_dm;
        font-size: 12px;
        font-weight: bold;

        ul > li {
            padding: 5px 20px 5px 15px;

            &.is-child {
                padding: 3px 20px 3px 20px;
                font-size: 12px;
                font-weight: normal;
                @include opacity(0.7);

                & + :not(.is-child) {
                    padding-top: 10px;
                }
            }

            a {
                display: block;
                position: relative;
                padding: 0 0 0 5px;
                letter-spacing: -0.1em;
                line-height: 1;
                word-break: keep-all;
                cursor: pointer;
                @include transition();

                &.is_active {
                    color: #333;
                }

                &:hover {
                    text-decoration: underline;
                }
            }
        }

        .active_bar {
            position: absolute;
            top: 11px;
            left: 10px;
            width: 3px;
            height: 10px;
            border-radius: 50px;
            background: #52beba;
            transition: all 0.3s;
        }
    }

    // TOP 버튼
    .top_btn {
        width: 146px;
        height: 30px;
        font-weight: 600;
        border: 1px solid #e6e6e6;
        border-radius: 100px;
        background: #ffffff;

        &::before {
            font-family: 'icomoon';
            display: inline;
            content: '\e010';
            transition: color 180ms ease-in;
        }

        &:hover {
            &::before {
                color: #52beba;
            }
        }
    }

    @media ($mq_mobile) {
        // Mobile
        display: none;
    }
}
