@charset "utf-8";
#notification {
    > .wrap {
        position: fixed;
        top: 10px;
        right: 10px;
        height: 0;
        z-index: 10;

        @media ($mq_mobile) {
            // Mobile
            width: calc(100% - 20px);
        }
    }

    .item {
        position: relative;
        width: 450px;
        min-height: 38px;
        padding: 20px 15px 20px 68px;
        margin: 0 0 3px 0;
        border-radius: 5px;
        box-sizing: border-box;
        overflow: hidden;
        color: #ffffff;
        z-index: 2;
        @include shadow(0, 3px, 3px, 0.2, 0, 0, 0);

        @media ($mq_mobile) {
            // Mobile
            width: auto;
            padding: 8px 15px 8px 68px;
        }

        > div {
            display: flex;
            flex-wrap: nowrap;
            align-items: center;

            > * {
                &:first-child {
                    flex-grow: 1;
                }
                &:not(:first-child) {
                    margin-left: 5px;
                }
            }
            button {
                padding: 5px 10px;
                border: 1px solid rgba(255, 255, 255, 0.4);
                border-radius: 4px;
                background: rgba(255, 255, 255, 0.1);
                line-height: 1;
                @include transition();

                &:hover {
                    border: 1px solid rgba(255, 255, 255, 0.6);
                    background: rgba(255, 255, 255, 0.3);
                }
            }
        }

        &:before {
            content: '';
            position: absolute;
            top: 50%;
            left: 15px;
            width: 38px;
            height: 38px;
            margin: -19px 0 0 0;
            background-size: 100% 100%;
        }

        &.is-error {
            background: linear-gradient(135deg, #ff5c41 0%, #d8332f 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr=#ff5c41, endColorstr=#d8332f, GradientType=1 ); /* IE6-9 */

            &:before {
                background-image: url('data:image/svg+xml;base64, ');
            }
        }

        &.is-info {
            background: linear-gradient(135deg, #a868d9 0%, #6253ce 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr=#A868D9, endColorstr=#6253CE, GradientType=1 ); /* IE6-9 */

            &:before {
                background-image: url('data:image/svg+xml;base64, ');
            }
        }

        &.is-complete {
            background: linear-gradient(135deg, #1ab5ab 0%, #2386de 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr=#1AB5AB, endColorstr=#2386DE, GradientType=1 ); /* IE6-9 */

            &:before {
                background-image: url('data:image/svg+xml;base64, ');
            }
        }
    }
}
