@charset "utf-8";

// 로케이션
.ui_location {
    position: sticky;
    top: 40px;
    // width: 100%;
    max-width: $doc_max_wid - 2;
    margin: 0 auto;
    border-bottom: 1px solid #dcdcdc;
    background: #ececec;
    box-sizing: border-box;
    line-height: 20px;
    z-index: 7;
    @include transition(0.2);

    > .wrap {
        position: relative;
        max-width: $doc_max_wid - 2px;
        min-height: 30px;
        margin: 0 auto;
        overflow: hidden;
        @include transition(0.2);

        > .inner_wrap {
            position: relative;
            padding: 0 0 0 50px;
            margin: 0 auto;
            box-sizing: border-box;
            @include clearBoth();

            &:before {
                content: '\e015';
                position: absolute;
                top: 5px;
                left: 20px;
                width: 20px;
                height: 20px;
                border-radius: 50%;
                background: #c2c2c2;
                font-family: 'icomoon';
                color: #ffffff;
                font-size: 12px;
                text-align: center;
                line-height: 20px;
            }

            .item {
                float: left;
                position: relative;
                padding: 5px 10px 5px 0;

                &:not(:first-child) {
                    padding: 5px 10px 5px 18px;

                    &:before {
                        content: '';
                        position: absolute;
                        top: 50%;
                        left: 0;
                        margin: -5px 0 0 0;
                        @include triangle('right', #d9d9d9, 8px, 10px);
                    }
                }
            }
        }
    }

    &.is-no-topsearchs {
        border: 1px solid #dcdcdc;
        border-radius: 0 0 5px 5px;

        > .wrap {
            // padding: 60px 0 0 0;
            // margin: -60px auto 0;
            // border: 1px solid #dcdcdc;
            // border-radius: 5px;
            // @include shadow(0, 3px, 8px, 0.15, 0, 0, 0);
        }

        // & + .contents {
        //     padding-top: 50px;
        // }
    }

    @media ($mq_mobile) {
        // Mobile
        // position: relative;
        // top: auto !important;
        // left: auto;
        // padding: 0 !important;
        // margin: 10px 0 0 0;
        // z-index: 0;

        top: 54px !important;
        border-bottom: 1px solid #dcdcdc;

        &.is-no-topsearchs {
            border: none;
        }

        .wrap {
            padding: 0 !important;
            margin: 0 auto !important;
            border: none !important;
            // @include shadow( 0, 0, 0, 0, 0, 0, 0, "", !important );
        }

        .inner_wrap {
            padding: 0 0 0 30px !important;

            &:before {
                left: 5px !important;
            }
        }
    }
}
