.slider {
  position: relative;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  min-width: 380px !important;
  height: 30px;
}
.slider .input {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: calc(100% + 30px);
  height: 6px;
  margin: 0 -15px;
  background: none;
  pointer-events: none;
  appearance: none;
  opacity: 0;
  z-index: 20;
}
.slider .input::-webkit-slider-thumb {
  pointer-events: auto;
  appearance: none;
  width: 30px;
  height: 30px;
  cursor: pointer;
}
.slider .input_tick {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  height: 6px;
  pointer-events: none;
}
.slider .input_tick::-webkit-slider-thumb {
  appearance: none;
  display: none;
}
.slider .track {
  position: relative;
  width: 100%;
  height: 6px;
  background-color: #d4d4d4;
  border-radius: 5px;
  pointer-events: none;
}
.slider .track .range {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #808080;
  border-radius: 5px;
}
.slider .track .thumb {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 12px;
  height: 12px;
  background-color: #ffffff;
  border: 1px solid #d4d4d4;
  border-radius: 5px;
}
.slider .track .thumb.left {
  transform: translate(-50%, -50%);
}
.slider .track .thumb.right {
  right: 0;
  transform: translate(50%, -50%);
}
.slider .tick_list {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 0;
}
.slider .tick_list .tick_opt {
  position: relative;
  top: -10px;
  display: flex;
  justify-content: center;
  width: 0;
  height: 30px;
  font-size: 10px;
  line-height: 5;
  cursor: pointer;
}
.slider .bubble {
  box-sizing: border-box;
  position: absolute;
  top: -55%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 30px;
  padding: 5px 8px;
  font-size: 10px;
  line-height: 1;
  color: #ffffff;
  background-color: #808080;
  border-radius: 50px;
  pointer-events: none;
  opacity: 0;
  transition: opacity 200ms;
}
.slider .bubble::after {
  position: absolute;
  top: 93%;
  left: 50%;
  transform: translateX(-50%);
  content: "";
  display: block;
  border-width: 6px 6px;
  border-color: #808080 transparent transparent;
  border-style: solid;
  width: 0;
  height: 0;
}
.slider .bubble.right {
  right: 0;
  transform: translateX(50%);
}
.slider.is-small, .slider.dcp_switch.is-box {
  height: 24px;
}
.slider.is-small .input_tick, .slider.dcp_switch.is-box .input_tick {
  height: 4px;
}
.slider.is-small .track, .slider.dcp_switch.is-box .track {
  height: 4px;
}
.slider.is-small .track .thumb, .slider.dcp_switch.is-box .track .thumb {
  width: 9px;
  height: 9px;
}
.slider.is-small .bubble, .slider.dcp_switch.is-box .bubble {
  top: -70%;
}

