@charset "utf-8";

/* Design Component */
.dcp {
    @include inlineBlock();
    position: relative;
    max-width: 100%;
    color: inherit;
    line-height: 1;

    // InputBox
    &.inputBox {
        > .input {
            &.is-disabled {
                &:after {
                    @include opacity(0.3);
                }
            }

            .btn_passVisible {
                position: absolute;
                top: 0;
                right: 5px;
                height: 100%;
                padding: 0 0.4em;

                &::before {
                    content: '\e079';
                    font-family: 'icomoon';
                    @include opacity(0.5);
                    @include transition();
                }

                &.is-active {
                    &::before {
                        content: '\e076';
                        font-family: 'icomoon';
                        @include opacity(1);
                    }
                }
            }

            .btn_reset {
                position: absolute;
                top: 0;
                right: 8px;
                height: 100%;
                padding: 0 0.4em;
                color: inherit;

                &::before {
                    content: '\e019';
                    font-family: 'icomoon';
                }

                &[disabled] {
                    // color: $component_font_color_disabled !important;
                    @include opacity(0.4);
                }

                &[readonly] {
                    // color: $component_font_color_disabled !important;
                    @include opacity(0.4);
                }
            }

            .search {
                position: absolute;
                top: 0;
                right: 0;
                height: 100%;
                display: flex;
                justify-content: flex-end;

                .btn_reset {
                    position: relative;
                    right: 3px;
                }

                .btn_search {
                    height: 100%;
                    padding: 0 8px;
                    border-radius: 0 5px 5px 0;
                    color: #ffffff;
                    background-color: #000000;

                    &::before {
                        content: '\e007';
                        font-family: 'icomoon';
                    }
                }
            }

            &.is-pass {
                input {
                    padding-right: 40px;
                }
            }
        }

        input {
            display: block;
            position: relative;
            width: 100%;
            padding: 6px 30px 6px 10px;
            border: 1px solid $component_brd_color;
            border-radius: 4px;
            box-sizing: border-box;
            background: #ffffff;
            color: $component_font_color;
            text-align: left;
            line-height: 16px;
            outline: none;
            @include transition();

            &:hover {
                border: 1px solid $component_brd_color_over;
            }

            &:focus {
                border: 1px solid $component_brd_color_active;
                color: $component_font_color_active;
            }

            &[disabled] {
                border: 1px solid $component_brd_color_disabled;
                background: $component_bg_color_disabled;
                color: $component_font_color_disabled !important;
            }

            &[readonly] {
                border: 1px solid $component_brd_color_disabled;
                background: #eeeeee;
                color: $component_font_color_disabled !important;
                cursor: default;
            }
        }

        &.search {
            input {
                padding-right: 49px;
            }
        }

        &.disabled_reset {
            input {
                padding-right: 10px;
            }
        }

        &.number {
            input {
                padding-right: 10px;
            }
        }

        > .list {
            position: fixed;
            z-index: 3;

            > .inner_wrap {
                position: relative;
                max-height: 500px;
                min-height: 30px;
                padding: 3px;
                margin: 1px 0 0 0;
                border: 1px solid $component_brd_color;
                border-radius: 4px;
                background: #ffffff;
                box-sizing: border-box;
                overflow: auto;
                @include shadow(0, 1px, 2px, 0.16, 0, 0, 0);
            }

            ul {
                display: block;
                position: relative;
                //@include transition();

                > li {
                    padding: 0 0 1px 0;

                    &:last-child {
                        padding-bottom: 0;
                    }

                    > .item {
                        display: block;
                        width: 100%;
                        padding: 3px 6px;
                        box-sizing: border-box;
                        text-align: left;
                        line-height: 16px;
                        // @include transition( 0.18, 0, ( background, color ) );

                        &:hover {
                            background: $component_bg_color_over;
                        }

                        &.is-active {
                            background: $component_bg_color_active;
                            color: #ffffff;

                            > * {
                                color: #ffffff !important;
                            }
                        }

                        &[disabled] {
                            background: $component_bg_color_disabled;
                            color: $component_font_color_disabled;
                        }

                        > strong {
                            color: #de521f;
                            // @include transition();
                        }
                    }
                }
            }

            .multiple_select_list {
                display: block;
                position: relative;
                text-align: left;
                // @include transition( 0, 0 );

                > .all {
                    display: block;
                    position: relative;
                    height: 34px;
                    padding: 0 10px;
                    margin: -3px -3px 0;
                    border-bottom: 1px solid #c7c7c7;
                    line-height: 34px;

                    .dcp_switch {
                        position: absolute;
                        top: 6px;
                        right: 6px;
                    }
                }

                .checked_list {
                    position: relative;
                    padding: 3px;
                    margin: -3px -3px 0;
                    background: #e6e6e6;

                    > strong {
                        display: block;
                        position: relative;
                        padding: 7px 0 7px 21px;
                        line-height: 16px;
                        @include clearBoth();

                        &:before {
                            content: '';
                            position: absolute;
                            top: 11px;
                            left: 6px;
                            width: 10px;
                            height: 8px;
                            background: url('data:image/svg+xml;base64, PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIxMCIgaGVpZ2h0PSI4IiB2aWV3Qm94PSIwIDAgMTAgOCI+PHBhdGggZmlsbD0iIzQ4NDg0OCIgZD0iTTg5Ni40MjIsNTI5LjRsLTQuMTI5LTMuODYxLjg1Ny0uOCwzLjE0MSwyLjkzNyw1LjAzMy02LjI3NS45NjkuNjhaIiB0cmFuc2Zvcm09InRyYW5zbGF0ZSgtODkyLjI5MyAtNTIxLjQpIi8+PC9zdmc+') no-repeat center center;
                        }

                        .total {
                            float: right;
                            padding: 0 0 0 15px;
                            color: $component_font_color;
                            font-weight: normal;

                            > strong {
                                padding: 0 5px;
                                color: $component_font_color_active;
                            }
                        }
                    }

                    & + .unchecked_list {
                        padding-top: 3px;
                        border-top: 1px solid #c7c7c7;
                    }
                }

                .unchecked_list {
                    position: relative;
                    padding: 3px;
                    margin: 0 -3px;
                }

                ul {
                    min-height: 0;
                    max-height: none;
                    padding: 0;
                    margin: 0;
                    border: none;
                    border-radius: 0;
                    background: transparent;
                    @include shadow(0, 0, 0, 0, 0, 0, 0);

                    > li {
                    }
                }
            }
        }

        &.is-btns {
            white-space: nowrap;

            > .input {
                @include inlineBlock();
                vertical-align: top;

                input {
                    border-radius: 4px 0 0 4px;
                    z-index: 1;

                    // &:hover, &:focus {
                    //     z-index:
                    // }
                }

                & + .btns {
                    @include inlineBlock();
                    padding: 6px 0;
                    margin-left: -5px;
                    border: 1px solid $component_brd_color;
                    border-radius: 0 4px 4px 0;
                    box-sizing: border-box;
                    background: #ffffff;
                    color: $component_font_color;
                    line-height: 16px;
                    vertical-align: top;
                    z-index: 0;
                    @include transition();

                    &.is-disabled {
                        border: 1px solid $component_brd_color_disabled;
                        background: $component_bg_color_disabled;
                        color: $component_font_color_disabled !important;

                        > button > *,
                        > button:before,
                        > button:after {
                            @include opacity(0.3);
                        }
                    }

                    &:hover:not(.is-disabled),
                    &:focus:not(.is-disabled) {
                        border: 1px solid $component_brd_color_over;
                        z-index: 1;
                    }
                }
            }
        }

        &.is-autoComplete {
            > .input {
                input {
                    padding-right: 36px !important;
                }

                &:after {
                    content: '\e056';
                    position: absolute;
                    top: 0;
                    right: 10px;
                    font-family: 'icomoon';
                    font-size: 16px;
                    line-height: 30px;
                }
            }

            &.is-btns {
                > .input {
                    width: calc(100% - 29px);

                    &:after {
                        right: 38px !important;
                        z-index: 2;
                    }

                    & + .btns {
                        position: relative;
                        width: 30px;
                        height: 30px;

                        .select_arrows {
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;

                            > span {
                                display: none;
                            }

                            &:before {
                                content: '\e041';
                                position: absolute;
                                top: 50%;
                                left: 50%;
                                transform: translateY(-50%) translateX(-50%);
                                font-family: 'icomoon';
                                color: $font_color;
                                font-size: inherit;
                                line-height: 1;
                                @include transition();
                            }

                            &:after {
                                content: '\e040';
                                position: absolute;
                                top: 50%;
                                left: 50%;
                                transform: translateY(-50%) translateX(-50%);
                                margin-top: 6px;
                                font-family: 'icomoon';
                                color: $font_color_hl;
                                font-size: inherit;
                                line-height: 1;
                                @include transition();
                                @include opacity(0);
                            }
                        }
                    }
                }
            }
        }

        &.is-expanded {
            &.is-btns {
                > .input {
                    & + .btns {
                        border: 1px solid $component_brd_color_over;
                        z-index: 1;

                        .select_arrows {
                            &:before {
                                margin-top: -6px;
                                @include opacity(0);
                            }
                            &:after {
                                margin-top: 0;
                                @include opacity(1);
                            }
                        }
                    }
                }
            }
        }
    }

    // InputBox
    &.textarea {
        display: block;

        textarea {
            display: block;
            width: 100%;
            padding: 6px 10px;
            border: 1px solid $component_brd_color;
            border-radius: 4px;
            box-sizing: border-box;
            background: #ffffff;
            color: $component_font_color;
            text-align: left;
            line-height: 1.5em;
            outline: none;
            resize: none;
            @include transition();

            &:hover {
                border: 1px solid $component_brd_color_over;
            }

            &:focus {
                border: 1px solid $component_brd_color_active;
                color: $component_font_color_active;
            }

            &[disabled] {
                border: 1px solid $component_brd_color_disabled;
                background: $component_bg_color_disabled;
                color: $component_font_color_disabled !important;
                text-decoration: line-through;
            }
        }
    }

    // Checkbox
    &.checkbox {
        padding: 6px 15px 6px 0;
        vertical-align: top;

        &.is-btn {
            padding: 0 4px 0 0;

            > input[type='checkbox'] {
                @include customComponent();

                & + label {
                    display: block;
                    position: relative;
                    padding: 6px;
                    // padding-left: 26px;
                    border: 1px solid $component_brd_color;
                    border-radius: 4px;
                    box-sizing: border-box;
                    background: #ffffff;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    color: $font_color;
                    text-align: center;
                    line-height: 16px;
                    cursor: pointer;
                    @include transition();

                    .icon {
                        display: none;
                        position: absolute;
                        top: 50%;
                        left: 6px;
                        width: 14px;
                        height: 14px;
                        margin: -8px 0 0 0;
                        border: 1px solid rgba(255, 255, 255, 0);
                        border-radius: 3px;
                        background: rgba(0, 0, 0, 0.08);
                        @include transition();

                        &:before {
                            content: '\e004';
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 14px;
                            height: 14px;
                            transform: scale(2);
                            color: #ffffff;
                            font-family: 'icomoon';
                            font-size: 12px;
                            font-weight: normal;
                            text-align: center;
                            line-height: 14px;
                            @include opacity(0);
                            @include transition();
                        }
                    }

                    &:after {
                        content: '';
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                    }
                }

                &:not([disabled]):hover + label,
                &:not([disabled]) + label:hover,
                &:focus:not([disabled]) + label,
                &:not([disabled]) + label:focus {
                    border: 1px solid $component_brd_color_over;
                    background-color: $component_bg_color_over;
                    //color: $point_color;
                }

                &:not([disabled]):checked + label {
                    border: 1px solid $component_brd_color_active !important;
                    background: $component_bg_color_active !important;
                    color: #ffffff;

                    .icon {
                        background: rgba(0, 0, 0, 0);

                        &:before {
                            transform: scale(1);
                            @include opacity(1);
                        }
                    }

                    &.customColor {
                        border-color: var(--color) !important;
                        background-color: var(--color) !important;
                        color: #ffffff !important;
                    }
                }

                &[disabled] + label {
                    border: 1px solid $component_brd_color_disabled !important;
                    background: $component_bg_color_disabled !important;
                    color: $component_font_color_disabled !important;
                    cursor: default;

                    .txt {
                        text-decoration: line-through;
                    }
                }
            }

            // > input[type='checkbox'] {
            //     &:not([disabled]):checked + label {
            //         &.customColor {
            //             border-color: var(--color) !important;
            //             background-color: var(--color) !important;
            //             color: #ffffff !important;
            //         }
            //     }
            // }

            // > input[type='checkbox'] {
            //     &:not([disabled]):checked + label {
            //         &.customColor {
            //             border-color: var(--color) !important;
            //             background-color: var(--color) !important;
            //             color: #ffffff !important;
            //         }
            //     }
            // }
        }

        &.is-box {
            @extend .dcp, .checkbox, .is-btn;
        }

        &:not(.is-btn):not(.is-box) {
            > input[type='checkbox'] {
                @include customComponent();

                & + label {
                    display: block;
                    position: relative;
                    padding: 0 0 0 23px;
                    color: $font_color;
                    line-height: 18px;
                    cursor: pointer;

                    .icon {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 18px;
                        height: 18px;
                        border: 1px solid $component_brd_color;
                        border-radius: 3px;
                        background: #ffffff;
                        box-sizing: border-box;
                        overflow: hidden;
                        @include transition();

                        &:before {
                            content: '\e004';
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 16px;
                            height: 16px;
                            transform: scale(2);
                            color: #ffffff;
                            font-family: 'icomoon';
                            font-size: 12px;
                            font-weight: normal;
                            text-align: center;
                            line-height: 16px;
                            @include opacity(0);
                            @include transition();
                        }
                    }

                    &:after {
                        content: '';
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                    }
                }

                &:hover + label,
                & + label:hover,
                &:focus:not([disabled]) + label {
                    .icon {
                        border: 1px solid $component_brd_color_active;
                        background-color: $component_bg_color_over;
                    }
                }

                &:checked + label {
                    color: $component_font_color_active;

                    .icon {
                        border: 1px solid $component_brd_color_active !important;
                        background: $component_bg_color_active !important;

                        &:before {
                            transform: scale(1);
                            @include opacity(1);
                        }
                    }
                }

                &[disabled] {
                    & + label {
                        color: $component_font_color_disabled;
                        cursor: default;

                        .icon {
                            border: 1px solid $component_brd_color;
                            background-color: $component_bg_color_disabled;
                            background-position: center 20;
                        }

                        .txt {
                            text-decoration: line-through;
                        }
                    }
                }
            }
        }

        &.allChk {
            padding-right: 0;

            label {
                font-weight: bold;
            }
        }

        &.is-not-label {
            padding: 6px 0;

            > input[type='checkbox'] {
                & + label {
                    width: 18px;
                    height: 18px;
                    padding: 0 !important;
                    overflow: hidden;
                }
            }
        }

        .icon {
            &.is-before,
            &.is-after {
                display: inline !important;
                position: relative !important;
                top: auto !important;
                left: auto !important;
                width: auto !important;
                height: auto !important;
                margin: 0 !important;
                background: transparent !important;
                font-family: 'icomoon';

                &:before,
                &:after {
                    content: none !important;
                }
            }
        }
    }

    // Radio
    &.radio {
        padding: 6px 15px 6px 0;
        vertical-align: top;

        &.is-btn {
            padding: 0;

            > input[type='radio'] {
                @include customComponent();

                & + label {
                    display: block;
                    position: relative;
                    padding: 6px;
                    // padding-left: 26px;
                    border: 1px solid $component_brd_color;
                    border-radius: 50px !important;
                    box-sizing: border-box;
                    background: #ffffff;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    color: $font_color;
                    text-align: center;
                    line-height: 16px;
                    cursor: pointer;
                    @include transition();

                    .icon {
                        display: none;
                        position: absolute;
                        top: 50%;
                        left: 6px;
                        width: 16px;
                        height: 16px;
                        margin: -8px 0 0 0;
                        border-radius: 50% !important;
                        background: rgba(0, 0, 0, 0.08);
                        box-sizing: border-box;
                        @include transition();
                    }

                    &:after {
                        content: '';
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                    }
                }

                &:not([disabled]):hover + label,
                &:not([disabled]) + label:hover,
                &:focus:not([disabled]) + label {
                    border: 1px solid $component_brd_color_over;
                    background-color: $component_bg_color_over;
                }

                &:not([disabled]):checked + label {
                    border: 1px solid $component_brd_color_active !important;
                    background: $component_bg_color_active !important;
                    color: #ffffff;

                    .icon {
                        border: 4px solid #ffffff !important;
                        background: transparent;
                    }
                }

                &[disabled] + label {
                    border: 1px solid $component_brd_color_disabled !important;
                    background: $component_bg_color_disabled !important;
                    color: $component_font_color_disabled !important;
                    cursor: default;

                    .txt {
                        text-decoration: line-through;
                    }
                }
            }
        }

        &.is-box {
            @extend .dcp, .radio, .is-btn;
        }

        &:not(.is-btn):not(.is-box) {
            > input[type='radio'] {
                @include customComponent();

                & + label {
                    display: block;
                    position: relative;
                    padding: 0 0 0 23px;
                    color: $font_color;
                    line-height: 18px;
                    cursor: pointer;

                    .icon {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 18px;
                        height: 18px;
                        border: 1px solid $component_brd_color;
                        border-radius: 50% !important;
                        background: #ffffff;
                        box-sizing: border-box;
                        overflow: hidden;
                        @include transition();

                        &:before {
                            content: '';
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            width: 0;
                            height: 0;
                            margin: 0;
                            border-radius: 50%;
                            box-sizing: border-box;
                            background: #ffffff;
                            @include transition();
                        }
                    }

                    &:after {
                        content: '';
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                    }
                }

                &:hover + label,
                & + label:hover,
                &:focus:not([disabled]) + label {
                    .icon {
                        border: 1px solid $component_brd_color_active;
                        background-color: $component_bg_color_over;
                    }
                }

                &:checked + label {
                    color: $component_font_color_active;

                    .icon {
                        border: 1px solid $component_brd_color_active !important;
                        background: #808080 !important;

                        &:before {
                            width: 10px;
                            height: 10px;
                            margin: -5px 0 0 -5px;
                        }
                    }
                }

                &[disabled] {
                    & + label {
                        color: $component_font_color_disabled;
                        cursor: default;

                        .icon {
                            border: 1px solid $component_brd_color;
                            background-color: $component_bg_color_disabled;
                            background-position: center 20;
                        }

                        .txt {
                            text-decoration: line-through;
                        }
                    }
                }
            }
        }

        &.is-not-label {
            padding: 6px 0;

            > input[type='checkbox'] {
                & + label {
                    width: 18px;
                    height: 18px;
                    padding: 0 !important;
                    overflow: hidden;
                }
            }
        }

        &.is-btn,
        &.is-box,
        &:not(.is-btn):not(.is-box) {
            > input[type='radio'] {
                & + label {
                    .icon {
                        &.is-before,
                        &.is-after {
                            display: inline !important;
                            position: relative !important;
                            top: auto !important;
                            left: auto !important;
                            width: auto !important;
                            height: auto !important;
                            margin: 0 !important;
                            border: none !important;
                            background: transparent !important;
                            font-family: 'icomoon';

                            &:before,
                            &:after {
                                content: none !important;
                            }
                        }
                    }
                }

                &:checked {
                    & + label {
                        .icon {
                            &.is-before,
                            &.is-after {
                                border: none !important;
                            }
                        }
                    }
                }
            }
        }
    }

    // Select
    &.selectBox {
        > select {
            position: relative;
            min-width: 80px;
            height: 30px;
            padding-right: 25px;
            border: 1px solid $component_brd_color;
            border-radius: 5px;
            visibility: hidden;

            & + label {
                display: none;
            }

            &.is-multiple {
                min-width: 150px;
            }
        }

        > .wrap {
            // position: absolute;
            // top: 0;
            // width: 100%;
            // height: 100%;

            min-width: 120px;

            .inputs {
                display: block;
                position: relative;
                //min-width: 100px;
                width: 100%;
                height: 100%;
                padding: 6px 22px 6px 10px;
                border: 1px solid $component_brd_color;
                border-radius: 4px;
                background: #ffffff;
                box-sizing: border-box;
                color: $component_font_color;
                text-align: left;
                line-height: 16px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                @include transition();

                input {
                    width: 100%;
                    height: 100%;
                    border: none;
                    outline: none;
                    line-height: 100%;

                    & + label {
                        position: absolute;
                        top: 0;
                        right: 0;
                        width: 24px;
                        height: 100%;
                        cursor: pointer;
                        z-index: 1;
                    }
                }

                &:before {
                    content: '\e041';
                    position: absolute;
                    top: 50%;
                    left: auto !important;
                    right: 8px;
                    transform: translateY(-50%);
                    font-family: 'icomoon';
                    color: $font_color;
                    font-size: 12px;
                    line-height: 1;
                    @include transition();
                }

                &:after {
                    content: '\e040';
                    position: absolute;
                    top: 50%;
                    left: auto !important;
                    right: 8px;
                    transform: translateY(-50%);
                    margin-top: 6px;
                    font-family: 'icomoon';
                    color: $font_color_hl;
                    font-size: 12px;
                    line-height: 1;
                    @include transition();
                    @include opacity(0);
