                        line-height: 50px;
                    }
                }

                &.is-btns {
                    > .input {
                        width: calc(100% - 49px);

                        &:after {
                            right: 58px !important;
                        }

                        & + .btns {
                            width: 50px;
                        }
                    }
                }
            }
        }

        &.checkbox {
            &.is-btn,
            &.is-box {
                padding-top: 0;
                padding-bottom: 0;

                > input[type='checkbox'] {
                    & + label {
                        padding: 14px;
                        font-size: 1.333em;
                        line-height: 20px;

                        .icon {
                            left: 12px;
                            width: 22px;
                            height: 22px;
                            margin: -12px 0 0 0;

                            &:before {
                                width: 20px;
                                height: 20px;
                                font-size: 20px;
                                line-height: 20px;
                            }
                        }
                    }
                }
            }

            &:not(.is-btn):not(.is-box) {
                padding-top: 13px;
                padding-bottom: 13px;

                > input[type='checkbox'] {
                    & + label {
                        padding: 0 0 0 28px;
                        line-height: 24px;
                        font-size: 1.333em;

                        .icon {
                            width: 24px;
                            height: 24px;

                            &:before {
                                width: 22px;
                                height: 22px;
                                font-size: 16px;
                                line-height: 22px;
                            }
                        }
                    }
                }
            }

            &.is-not-label {
                > input[type='checkbox'] {
                    & + label {
                        width: 24px;
                        height: 24px;
                    }
                }
            }
        }

        &.radio {
            &.is-btn,
            &.is-box {
                padding-top: 0;
                padding-bottom: 0;

                > input[type='radio'] {
                    & + label {
                        padding: 14px;
                        font-size: 1.333em;
                        line-height: 20px;

                        .icon {
                            left: 12px;
                            width: 22px;
                            height: 22px;
                            margin: -11px 0 0 0;
                        }
                    }

                    &:checked + label {
                        .icon {
                            border-width: 6px;
                        }
                    }
                }
            }

            &:not(.is-btn):not(.is-box) {
                padding-top: 13px;
                padding-bottom: 13px;

                > input[type='radio'] {
                    & + label {
                        padding: 0 0 0 28px;
                        line-height: 24px;
                        font-size: 1.333em;

                        .icon {
                            width: 24px;
                            height: 24px;
                        }
                    }

                    &:checked + label {
                        .icon {
                            &:before {
                                width: 14px;
                                height: 14px;
                                margin: -7px 0 0 -7px;
                            }
                        }
                    }
                }
            }

            &.is-not-label {
                > input[type='radio'] {
                    & + label {
                        width: 24px;
                        height: 24px;
                    }
                }
            }
        }

        &.selectBox {
            > select {
                height: 50px;
                padding-right: 50px;
                font-size: 1.333em;
            }

            > .wrap {
                > .inputs {
                    padding: 16px 38px 16px 20px;
                    font-size: 1.333em;

                    &:before {
                        font-size: 20px;
                    }

                    &:after {
                        font-size: 20px;
                    }
                }
            }
        }
    }

    // Loading
    .in_loader {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        text-align: left;
        text-indent: -9999px;
        visibility: hidden;
        z-index: 10;
        @include backgroundAlpha(#ffffff, 0.8);
        @include opacity(0);
        @include transition();
        @include shadow(1px, 1px, 2px, 0.1);

        &:before {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            width: 15px;
            height: 15px;
            margin: -7px 0 0 -7px;
            border-radius: 7px;
            background: url(~static/img/ui/ui_loader_02.gif) no-repeat center center;
        }
    }
    &.loading {
        .in_loader {
            visibility: visible;
            @include opacity(1);
        }
    }

    @media ($mq_mobile) {
        // Mobile
    }
}

