// 토글 스위치
.dcp_switch {
    @include inlineBlock();
    vertical-align: top;
    position: relative;
    line-height: 1;

    > input {
        @include customComponent();

        & + label {
            display: block;
            position: relative;
            cursor: pointer;

            > .txt {
                @include inlineBlock();
                position: relative;
                padding: 0 5px 0 0;
                line-height: 30px;
                vertical-align: top;
            }

            .wrap {
                @include inlineBlock();
                position: relative;
                padding: 3px 0;
                vertical-align: top;
                pointer-events: none;

                .bg {
                    display: block;
                    position: relative;
                    width: 64px;
                    height: 24px;
                    border-radius: 50px !important;
                    border: 1px solid $component_brd_color;
                    background: #ffffff;
                    box-sizing: border-box;
                    overflow: hidden;
                    @include transition();

                    &:before {
                        content: 'ON';
                        position: absolute;
                        top: 50%;
                        left: -30px;
                        transform: translateY(-50%);
                        margin: -1px 0 0 0;
                        color: #ffffff;
                        line-height: 1;
                        @include transition(0.2);
                    }
                    &:after {
                        content: 'OFF';
                        position: absolute;
                        top: 50%;
                        right: 7px;
                        transform: translateY(-50%);
                        margin: -1px 0 0 0;
                        color: #959595;
                        line-height: 1;
                        @include transition(0.2);
                    }
                }

                .anchor {
                    position: absolute;
                    top: 0;
                    right: 34px;
                    width: 30px;
                    height: 30px;
                    border: 1px solid #f1f1f1;
                    border-radius: 50% !important;
                    background: #ffffff;
                    box-sizing: border-box;
                    overflow: hidden;
                    @include shadow(0, 1px, 2px, 0.35, 0, 0, 0);
                    // z-index: 1;
                    @include transition(0.3, 0, all, cubic-bezier(0.175, 0.885, 0.32, 1.2));

                    &:after {
                        content: '';
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translateX(-50%) translateY(-50%);
                        width: 14px;
                        height: 14px;
                        border-radius: 50%;
                        background: $component_bg_color_active;
                        color: $component_bg_color_active;
                        font-family: 'icomoon';
                        font-size: 14px;
                        font-weight: normal;
                        text-align: center;
                        line-height: 14px;
                        @include opacity(0);
                        @include transition();
                    }
                }
            }

            &:after {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: 1;
            }
        }

        &[type='checkbox'] + label {
            .wrap {
                .bg {
                    border-radius: 4px !important;
                }

                .anchor {
                    border-radius: 4px !important;

                    &:after {
                        content: '\e004';
                        border-radius: 0;
                        background: none;
                    }
                }
            }
        }

        &:hover + label,
        & + label:hover,
        &:focus:not([disabled]) + label {
            .wrap {
                .bg {
                    border: 1px solid $component_brd_color_over;
                    background: $component_bg_color_over;
                }
            }
        }

        &:checked + label {
            .wrap {
                .bg {
                    border: 1px solid $component_brd_color_active !important;
                    background: $component_bg_color_active !important;

                    &:before {
                        left: 8px !important;
                    }
                    &:after {
                        right: -30px !important;
                    }
                }

                .anchor {
                    right: 0 !important;

                    &:after {
                        @include opacity(1);
                    }
                }
            }
        }

        &[disabled] + label {
            .wrap {
                .bg {
                    border: 1px solid $component_brd_color_disabled;
                    background: $component_bg_color_disabled;
                    cursor: default;

                    &:before {
                        color: #afafaf;
                        text-decoration: line-through;
                    }
                    &:after {
                        color: #afafaf;
                        text-decoration: line-through;
                    }
                }

                .anchor {
                    background: #f3f3f3;
                    cursor: default;
                }
            }
        }
    }

    &.is-small {
        > input {
            & + label {
                > .txt {
                    padding: 0 5px 0 0;
                    line-height: 22px;
                }

                .wrap {
                    padding: 3px 0;

                    .bg {
                        width: 36px;
                        height: 16px;
                        border-radius: 8px;

                        &:before {
                            content: none;
                        }
                        &:after {
                            content: none;
                        }
                    }

                    .anchor {
                        right: 16px;
                        width: 22px;
                        height: 22px;
                    }
                }
            }

            &[type='radio'] + label {
                .anchor {
                    &:after {
                        width: 10px;
                        height: 10px;
                    }
                }
            }
        }
    }

    &.is-large {
        > input {
            & + label {
                > .txt {
                    padding: 0 5px 0 0;
                    font-size: 1.167em;
                    line-height: 36px;
                }

                .wrap {
                    padding: 3px 0;

                    .bg {
                        width: 68px;
                        height: 30px;
                        border-radius: 15px;
                    }

                    .anchor {
                        right: 32px;
                        width: 36px;
                        height: 36px;

                        &:after {
                            width: 16px;
                            height: 16px;
                            font-size: 16px;
                            line-height: 16px;
                        }
                    }
                }
            }
        }
    }

    &.is-xlarge {
        > input {
            & + label {
                > .txt {
                    padding: 0 5px 0 0;
                    font-size: 1.333em;
                    line-height: 50px;
                }

                .wrap {
                    padding: 7px 0;

                    .bg {
                        width: 82px;
                        height: 36px;
                        border-radius: 18px;

                        &:before {
                            font-size: 1.167em;
                        }
                        &:after {
                            font-size: 1.167em;
                        }
                    }

                    .anchor {
                        top: 3px;
                        right: 38px;
                        width: 44px;
                        height: 44px;

                        &:after {
                            width: 20px;
                            height: 20px;
                            font-size: 20px;
                            line-height: 20px;
                        }
                    }
                }
            }
        }
    }

    &.is-box {
        @extend .is-small;

        display: block;

        input {
            & + label {
                min-width: 100px;
                height: 30px;
                padding: 3px 10px;
                border-radius: 4px;
                background: #ebebeb;
                box-sizing: border-box;

                > .txt {
                    float: left;
                    font-weight: normal;
                }
                > .wrap {
                    float: right;
                }
            }

            &[disabled] {
                & + label {
                    background: #d9d9d9;

                    > .txt {
                        color: #999999;
                        text-decoration: line-through;
                    }
                }
            }
        }
    }
}

// 멀티플 옵션 셀렉트 박스
.ui_multi_opts_select {
    line-height: 30px;

    > .input {
        display: block;
        position: relative;
        padding: 0 10px;
        margin: 0 0 5px 0;
        border-radius: 4px;
        box-sizing: border-box;
        background: #999999;
        box-sizing: border-box;
        color: #fff;
        font-size: 1.083em;
        text-align: center;
        cursor: pointer;
        outline: none;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;

        > * {
            position: relative;
            z-index: 2;
        }
    }

    > .list {
        position: absolute;
        width: 100%;
        z-index: 1;

        > .wrap {
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            width: 97%;
            padding: 3px;
            border: 1px solid #d4d4d4;
            border-radius: 5px;
            background: #fff;
            @include shadow(0, 1px, 2px, 0.16, 0, 0, 0);

            > .list_option {
                display: flex;
                justify-content: space-evenly;
                padding: 10px 0;

                > * {
                    display: flex;
                    flex: 0 0 100px;
                    flex-direction: column;

                    > .dcp {
                        &:not(:first-child) {
                            margin-top: 4px;
                        }
                    }
                }
            }
        }
    }

    &.is-large {
        line-height: 36px;
    }
    &.is-xlarge {
        line-height: 50px;
    }
}
