@charset "utf-8";

// 목록 게시판
.ui_brd {
    width: 100%;
    box-sizing: border-box;

    // 게시판 상단 - 게시판 정보
    > .infos {
        display: flex;
        position: relative;
        padding: 0 0 6px 0;
        line-height: 1;

        > * {
            display: flex;

            > * {
                align-self: center;
            }
        }

        > .lc {
        }
        > .rc {
            margin-left: auto;
        }

        hr {
            display: block;
            width: 1px;
            height: 1em;
            margin: 0 10px;
            border: none;
            background: $component_brd_color;
        }

        .page_info {
            display: flex;
            margin-right: 15px;
            color: $font_color_dm;

            &:last-child {
                margin-right: 0;
            }

            strong {
                color: $font_color;
            }

            > * {
                align-self: center;
            }
        }
    }

    // 게시판 탭
    > .tab {
        position: relative;
        margin-bottom: -1px;
        border-bottom: 1px solid $component_brd_color_active;
        z-index: 1;
    }

    // 실제 게시판 공간
    > .wrap {
        position: relative;
        overflow: auto;

        > table {
            width: 100%;
            table-layout: fixed;

            @at-root .ui_box.is-3dp & {
                background: #f8f8f8;
            }

            th,
            td {
                &.is-not-pad {
                    padding: 0 !important;
                }

                &.is-right-bd {
                    border-right: 1px solid #cccccc !important;
                }
            }

            > thead {
                position: sticky;
                top: 0;
                background: none;
                z-index: 1;

                > tr {
                    > th {
                        position: relative;
                        height: 22px;
                        padding: 9px 5px;
                        border: 1px solid #bebebe;
                        border-left-width: 0;
                        //border-right-width: 0;
                        background: #d2d2d2;
                        // line-height: 20px;
                        text-align: center;
                        @include transition(0.12, 0, (background, color));

                        &:first-child {
                            border-left-width: 1px;
                        }
                        &:last-child {
                            border-right-width: 1px;
                        }

                        &.is-left-bd {
                            border-left-width: 1px;
                        }

                        &.has-sort {
                            padding-top: 4px;
                            padding-bottom: 4px;

                            @media ($mq_mobile) {
                                // Mobile
                                padding: 4px 2px;
                                white-space: nowrap;

                                .ui_table_sort {
                                    margin-left: 5px;
                                }
                            }
                        }
                    }

                    &:not(:first-child) {
                        > th {
                            border-top-width: 0;

                            &:first-child {
                                border-left-width: 0;
                            }
                        }
                    }
                }

                &:after {
                    content: '';
                    display: block;
                    height: 4px;
                }
            }

            > tbody {
                > tr {
                    > th,
                    > td {
                        height: 22px;
                        padding: 4px 5px;
                        border: 1px solid $board_brd_color;
                        border-left-width: 0;
                        border-right-width: 0;
                        border-bottom: none;
                        background: #ffffff;
                        font-weight: normal;
                        text-align: center;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        line-height: 22px;
                        overflow: hidden;
                        @include transition(0.12);

                        &:first-child {
                            border-left-width: 1px;
                        }
                        &:last-child {
                            border-right-width: 1px;
                        }

                        &.is-not-left-bd {
                            border-left: none;
                        }

                        &.is-bottom-bd {
                            border-bottom: 1px solid $board_brd_color;
                        }

                        // 수치형 셀일 때 우측 정렬
                        &.is-number {
                            text-align: right;
                        }

                        .is-title-txts {
                            display: flex;
                            flex-direction: column;

                            > * {
                                // @include inlineBlock();
                                max-width: 100%;
                                padding: 4px 0;
                                vertical-align: top;
                                white-space: nowrap;
                                overflow: hidden;
                                text-overflow: ellipsis;
                                line-height: 1;
                            }

                            // 검색어
                            > * {
                                .is-anal {
                                    color: $anal_keyword_color;
                                    font-weight: bold;
                                }
                                .is-search {
                                    color: $anal_keyword_color;
                                    font-weight: bold;
                                }
                                .is-rk {
                                    color: $anal_keyword_color;
                                    font-weight: bold;
                                }
                            }
                        }

                        .is-selected-rk {
                            height: 22px;
                            padding: 0 15px;
                            border: 1px solid $component_brd_color;
                            border-radius: 100px;
                            background: #ffffff;
                            box-sizing: border-box;
                            line-height: 20px;
                            border-color: var(--color);
                            background-color: var(--color);
                            color: #ffffff;
                        }

                        // 수집 키워드 하이라이트
                        .is-hl-colKeyword {
                            font-weight: bold;
                            text-decoration: underline;
                        }

                        // 인풋 키워드 하이라이트
                        .is-hl-inputkeyword {
                            padding: 1px 3px;
                            background: #b3b3b3;
                            color: #ffffff;
                        }

                        // 연관어 키워드 하이라이트
                        .is-hl-rkeyword {
                            padding: 1px 5px;
                            border-radius: 100px;
                        }
                    }

                    &:last-child {
                        > th,
                        > td {
                            border-bottom: 1px solid $board_brd_color;
                        }
                    }

                    &:nth-child(2n) {
                        > th,
                        > td {
                            background: $board_bg_color_even;
                        }
                    }

                    &.is-active {
                        > th,
                        > td {
                            @include backgroundAlpha($point_color, 0.2);
                            color: $font_color_hl !important;
                        }
                    }

                    &.is-highlight {
                        > th,
                        > td {
                            @include backgroundAlpha($point_color_2, 0.2);
                        }
                    }

                    .lnk {
                        &:hover {
                            text-decoration: underline;
                        }
                    }
                    .lnk_not_ani {
                        @include inlineBlock();

                        &:hover {
                            text-decoration: underline;
                        }
                    }
                    .col_none {
                        padding: 5px 0 5px 5px;
                        border-right: none;
                    }
                }
            }

            > tfoot {
                > tr {
                    > th,
                    > td {
                        position: relative;
                        height: 22px;
                        padding: 10px 5px;
                        border-top: 1px solid $brd_color_dm;
                        background: #f0f0f0;
                        color: #444444;
                        text-align: center;
                        line-height: 1.4em;

                        &:after {
                            content: '';
                            position: absolute;
                            top: 50%;
                            right: 0;
                            width: 1px;
                            height: 14px;
                            margin: -7px 0 0 0;
                            background: #bfbfbf;
                        }

                        &:last-child {
                            &:after {
                                content: none;
                            }
                        }
                    }
                }
            }

            @media ($mq_mobile) {
                // Mobile
                //min-width: 100%;
                //width: auto !important;
                table-layout: auto;

                colgroup {
                    display: none;
                }

                th,
                td {
                    width: auto !important;
                }
            }
        }

        // 상단 보더 없는 경우
        &.no_bd {
            > table {
                border-top: none !important;
            }
        }

        &.h_scroll {
            overflow-x: auto;

            > table {
                width: auto;
                min-width: 100%;
            }
        }

        @at-root .ui_box.dims & {
            border-top: 1px solid $brd_color_dm;
        }
    }

    &.is-multiline {
        > .wrap {
            max-height: 386px;

            > table {
                > thead {
                    > tr {
                        > th {
                            padding: 6px 5px;
                        }

                        &:not(:first-child) {
                            span,
                            strong {
                                font-size: 11px;
                                font-weight: normal;
                            }
                        }
                    }
                }
            }
        }
    }

    // TR Over
    &:not(.is-no-over):not(.is-data):not(.is-data-v2) {
        > .wrap {
            > table {
                > tbody {
                    > tr {
                        &:hover:not(.is-no-over) {
                            > th:not([rowspan]):not(.is-no-over):not(.no-data),
                            > td:not([rowspan]):not(.is-no-over):not(.no-data) {
                                background-color: $board_bg_color_hover;
                            }

                            &.is-active {
                                > th:not([rowspan]):not(.is-no-over):not(.no-data),
                                > td:not([rowspan]):not(.is-no-over):not(.no-data) {
                                    @include backgroundAlpha($point_color, 0.3, !important);
                                }
                            }

                            &.is-highlight {
                                > th:not([rowspan]):not(.is-no-over):not(.no-data),
                                > td:not([rowspan]):not(.is-no-over):not(.no-data) {
                                    @include backgroundAlpha($point_color_2, 0.3, !important);
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    > .footer {
        padding: 20px 0 0;
        text-align: center;
    }

    // Width 사이즈 자동
    &.is-size-auto {
        @include inlineBlock();

        > .wrap {
            table {
                width: auto;
            }
        }
    }

    &.is-hgt-fixed {
        > .wrap {
            max-height: 357px;
        }

        &.is-body-twoline-mode {
            > .wrap {
                max-height: 517px;
            }
        }

        &.is-data-v2 {
            > .wrap {
                max-height: 362px;
            }
        }
    }

    // 데이터 테이블
    &.is-data {
        > .wrap {
            > table {
                border-spacing: 0;

                > thead {
                    > tr {
                        > th {
                            border-right-width: 1px;
                        }
                    }
                }

                > tbody {
                    > tr {
                        > th {
                            border-right-width: 1px;
                            border-bottom-width: 1px;
                            background: $board_th_bg_color;

                            &.is-active {
                                @include backgroundAlpha($point_color, 0.3);
                                color: $font_color_hl !important;
                            }

                            &.is-highlight {
                                @include backgroundAlpha($point_color_2, 0.3);
                            }
                        }

                        > td {
                            border-right-width: 1px;
                            border-bottom-width: 1px;

                            &.is-active {
                                @include backgroundAlpha($point_color, 0.2);
                                color: $font_color_hl !important;
                            }

                            &.is-highlight {
                                @include backgroundAlpha($point_color_2, 0.2);
                            }
                        }

                        &.is-active {
                            > th {
                                @include backgroundAlpha($point_color, 0.2);
                            }
                            > td {
                                @include backgroundAlpha($point_color, 0.1);
                            }
                        }

                        &.is-highlight {
                            > th {
                                @include backgroundAlpha($point_color_2, 0.3);
                            }
                            > td {
                                @include backgroundAlpha($point_color_2, 0.2);
                            }
                        }

                        &:hover:not(.is-no-over) {
                            > th:not(.no-data):not(.is-no-over) {
                                background: $board_th_bg_color_hover;

                                &.is-active {
                                    @include backgroundAlpha($point_color, 0.3);
                                    color: $font_color_hl !important;
                                }

                                &.is-highlight {
                                    @include backgroundAlpha($point_color_2, 0.4);
                                }
                            }
                            > td:not(.no-data):not(.is-no-over) {
                                background: $board_bg_color_hover;

                                &.is-active {
                                    @include backgroundAlpha($point_color, 0.2);
                                    color: $font_color_hl !important;
                                }

                                &.is-highlight {
                                    @include backgroundAlpha($point_color_2, 0.3);
                                }
                            }

                            &.is-active {
                                > th {
                                    @include backgroundAlpha($point_color, 0.3, !important);
                                }
                                > td {
                                    @include backgroundAlpha($point_color, 0.2, !important);
                                }
                            }

                            &.is-highlight {
                                > th {
                                    @include backgroundAlpha($point_color_2, 0.4, !important);
                                }
                                > td {
                                    @include backgroundAlpha($point_color_2, 0.3, !important);
                                }
                            }
                        }
                    }
                }

                > tfoot {
                    > tr {
                        > th,
                        > td {
                            padding: 10px;
                            border-right: 1px solid $brd_color_dm;

                            &:after {
                                content: none;
                            }

                            &:last-child {
                                border-right: none;
                            }
                        }
                    }
                }
            }
        }

        &.lnk_cell {
            > .wrap {
                tbody {
                    td {
                        &:not(.selected):hover {
                            background: #e9e9e9 !important;
                        }
                    }
                }
            }
        }
    }

    // 데이터 테이블 v2
    &.is-data-v2 {
        > .wrap {
            > table {
                > thead {
                    background: transparent;

                    > tr {
                        > th {
                            border: none;
                            border-radius: 0;
                            color: #000;

                            &:before {
                                background: #c7c7c7;
                            }

                            &:not(:last-child) {
                                &:before {
                                    border-right: 1px solid #ebebeb;
                                }
                            }

                            &:empty {
                                &:before {
                                    border: none;
                                    background: transparent;
                                }

                                & + th {
                                    &:before {
                                        border-top-left-radius: 5px;
                                        border-bottom-left-radius: 5px;
                                    }
                                }
                            }
                        }
                    }
                }

                > tbody {
                    > tr {
                        > th,
                        > td {
                            padding: 4px 5px 6px;
                            border: none;
                            border-radius: 0;
                            @include transition(0);

                            &:before {
                                height: calc(100% - 2px);
                                background: #f7f7f7;
                            }

                            &:not(:last-child) {
                                &:before {
                                    border-right: 1px solid #ededed;
                                }
                            }

                            &:hover:not(:empty):not(.no_data),
                            &.is-active:not(:empty):not(.no_data) {
                                color: #fff;

                                &:before {
                                    background: #666 !important;
                                }

                                > button.keyword {
                                    * {
                                        color: #ffffff !important;
                                    }
                                }
                            }

                            &.is-custom-active:not(:empty):not(.no_data) {
                                color: #fff;
                                &:before {
                                    background: var(--color) !important;
                                }
                                > button.keyword {
                                    * {
                                        color: #ffffff !important;
                                    }
                                }
                            }

                            > button.keyword {
                                display: flex;
                                width: 100%;
                                flex-wrap: nowrap;

                                > *:first-child {
                                    flex: 0 0 70%;
                                }
                                > *:last-child {
                                    flex: 0 0 30%;
                                    color: $font_color_dm;
                                    text-align: right;
                                }

                                &:hover {
                                    * {
                                        color: #ffffff !important;
                                    }
                                }
                            }
                        }

                        > th {
                            &:before {
                                background: #ddd;
                            }
                        }

                        &:last-child {
                            > th,
                            > td {
                                padding-bottom: 4px;

                                &:before {
                                    height: 100%;
                                }
                            }
                        }

                        // 크로스 오버 스타일
                        td.is-crossover {
                            &:before {
                                background: #e5e5e5;
                            }
                        }

                        // 짝수 컬럼일 경우 백그라운드 색상 변경
                        .is-even {
                            &:before {
                                border-right-color: #e1e1e1 !important;
                                background: #ededed !important;
                            }
                        }
                    }
                }

                th,
                td {
                    position: relative;
                    background: transparent;

                    &:before {
                        content: '';
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        box-sizing: border-box;
                    }

                    > * {
                        position: relative;
                    }

                    &:first-child {
                        &:before {
                            border-top-left-radius: 5px;
                            border-bottom-left-radius: 5px;
                        }
                    }
                    &:last-child {
                        &:before {
                            border-top-right-radius: 5px;
                            border-bottom-right-radius: 5px;
                        }
                    }

                    &.is-custom-color {
                        &:before {
                            background: var(--color);
                        }
                    }
                }
            }
        }
    }

    &.is-sticky {
        > .wrap {
            overflow: auto;

            > table {
                min-width: 100%;

                thead {
                    position: sticky;
                    top: 0;
                    z-index: 1;
                }
            }

            .on_left_bd {
                border-left: 1px solid $brd_color_dm !important;
            }
        }

        &.vertical_scroll {
            > .wrap {
                > table {
                    //width: 100%;
                }
            }
        }
    }

    &.is-body-twoline-mode {
        > .wrap {
            tbody {
                th,
                td {
                    padding: 3px 5px;
                    height: 40px !important;
                }
            }
        }
    }

    &.not_cross_bg {
        > .wrap {
            > table {
                > tbody {
                    > tr {
                        > td {
                            background: #ffffff;

                            .is-title-txts {
                                > .txt {
                                    color: #a7a7a7;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    &.not_top_brd {
        // Top 보더 제거
        > .wrap {
            border-top: none !important;

            > table {
                tr {
                    &:first-child {
                        > th,
                        > td {
                            border-top: none !important;
                        }
                    }
                }
            }
        }
    }
    &.not_bot_brd {
        // Bottom 보더 제거
        > .wrap {
            border-bottom: none !important;

            > table {
                > tbody {
                    > tr {
                        &:last-child {
                            > td {
                                border-bottom: none !important;
                            }
                        }
                    }
                }
            }
        }
    }
}

// 입력 게시판
