                        //border-right-width: 0;
                        background: #d2d2d2;
                        line-height: 22px;
                        text-align: center;
                        @include transition(0.12, 0, (background, color));

                        &:first-child {
                            border-left-width: 1px;
                        }
                        &:last-child {
                            border-right-width: 1px;
                        }

                        &.is-left-bd {
                            border-left-width: 1px;
                        }

                        &.has-sort {
                            //padding-right: 25px !important;
                        }
                    }

                    &:not(:first-child) {
                        > th {
                            border-top-width: 0;

                            &:first-child {
                                border-left-width: 0;
                            }
                        }
                    }
                }

                &:after {
                    content: '';
                    display: block;
                    height: 4px;
                }
            }

            > tbody {
                > tr {
                    > th,
                    > td {
                        height: 30px;
                        padding: 5px;
                        border-bottom: 1px solid #f8f8f8;
                        //border-right: 1px solid $board_brd_color;
