// .is-disabled {
//     @include opacity(0.3);
// }
.ui_block {
    display: block;
}
.ui_inlineBlock {
    @include inlineBlock();
}
.is-wid100p {
    width: 100% !important;
    box-sizing: border-box;
}
.ui_fl {
    float: left;
}
.ui_fr {
    float: right;
}
.ui_al {
    // 텍스트 정렬(좌)
    text-align: left !important;
}
.ui_ac {
    // 텍스트 정렬(중)
    text-align: center !important;
}
.ui_ar {
    // 텍스트 정렬(우)
    text-align: right !important;
}
.ui_vt {
    // 상하 정렬(상)
    vertical-align: top !important;
}
.ui_vm {
    // 상하 정렬(Baseline)
    vertical-align: baseline !important;
}
.ui_vb {
    // 상하 정렬(하)
    vertical-align: bottom !important;
}

// 링크 텍스트
.ui_lnk {
    @include overUnderline($font_color, $font_color, $font_color);
}

// No Data - 이미지 형태
