// No Data - 텍스트형태
.ui_no_data_txt {
    position: relative;
    padding: 0 0 0 18px;
    color: $font_color_dm;

    &:before {
        content: '\e045';
        @include inlineBlock();
        margin: 0 5px 0 0;
        font-family: 'icomoon';
        font-size: 16px;
        vertical-align: top;
    }
}

// 아이콘
.ui_icon {
    font-family: 'icomoon';
}

// 상태박스
.ui_status_box {
    @include inlineBlock();
    padding: 0 8px;
    border-radius: 100px;
    background: #ffffff;
    line-height: 24px;

    &.is-wide {
        padding: 0 20px;
    }

    &.is-customColor {
        border-color: var(--color) !important;
        background-color: var(--color) !important;
        color: #ffffff !important;
    }
}

// loader
.ui_loader_container {
    position: relative;

    > .ui_loader {
        visibility: hidden;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 10;
        @include backgroundAlpha(#ffffff, 0.8);
        @include opacity(0);
        @include transition(0.18, 0);

        &:before {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            width: 50px;
            height: 50px;
            margin: -25px 0 0 -25px;
            border-radius: 50%;
            overflow: hidden;
            background: #ffffff;
        }

        .loader {
            display: block;
            position: absolute;
            top: 50%;
            left: 50%;
            width: 40px;
            height: 40px;
            margin: -20px 0 0 -20px;
            background: url(~static/img/ui/ui_loader_00.gif) no-repeat center center;
            font-size: 8px;
            text-align: center;
            line-height: 40px;
        }
    }

    &.loading {
        > .ui_loader {
            visibility: visible;
            @include opacity(1);
            @include transition(0.18, 0);
        }
    }
}
.ui_page_loader {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 999;

    .ui_loader {
        visibility: visible;
        @include backgroundAlpha(#000000, 0.8);
        @include opacity(1);
        @include transition(0.18, 0);
    }
}

// Input(text/textarea)
.ui_input {
    height: 26px;
    padding: 0 10px;
    border: 1px solid $component_brd_color;
    background: #ffffff;
    box-sizing: border-box;
    line-height: 24px;
    outline: none;
    @include transition(0.18, 0, (border, opacity));

    &:hover,
    &:focus {
        &:not([readonly]) {
            border: 1px solid #666666;
        }
    }
    &[readonly] {
        background: #efefef;
    }

    &.ui_mdfy_input {
        &[readOnly] {
            border: 1px solid rgba(0, 0, 0, 0);
            background: none;
        }
    }
}
.ui_input_iufo {
    display: block;
    padding: 0 0 0 5px;
    color: #a1a1a1;
    font-size: 0.917em;
    line-height: inherit;
}

// Textarea
.ui_textarea {
    display: block;
    padding: 10px;
    border: 1px solid $component_brd_color;
    background: #ffffff;
    line-height: 1.6em;
    resize: none;
    outline: none;
    @include transition();

    &:hover,
    &:focus {
        border: 1px solid $point_color;
    }
}

// Multiple Select
.ui_m_sel {
    position: relative;
    border: 1px solid $brd_color_dm;

    select {
        display: block;
        width: 100%;
        border: none;
        outline: none;

        option {
            padding: 3px 5px;
        }
    }
}

// In Search
.ui_in_search {
    position: relative;

    input {
        display: block;
        width: 100%;
        height: 26px;
        padding: 0 30px 0 6px;
        border: 1px solid #cccccc;
        box-sizing: border-box;
        background: #ffffff;
        outline: none;
        @include transition();

        &:focus {
            border: 1px solid $point_color;
        }
    }
    button {
        position: absolute;
        top: 1px;
        right: 1px;
        width: 24px;
        height: 24px;
        background: url(~static/img/ui/ui_btns_icon.png) no-repeat -25px -20px;
        text-align: left;
        text-indent: -9999px;
    }

    &:hover {
        input {
            @extend :focus;
        }
    }
}

// Paginate
.ui_paginate {
    position: relative;

    .wrap {
        @include inlineBlock();
        vertical-align: top;
        @include clearBoth();

        .btn_page {
            position: relative;
            float: left;
            min-width: 30px;
            height: 30px;
            padding: 0 7px;
            margin: 0 2px;
            border: 1px solid transparent;
            border-radius: 5px;
            background: transparent;
            box-sizing: border-box;
            text-align: center;
            line-height: 28px;
            @include transition();

            &.first,
            &.prev,
            &.next,
            &.last {
                padding: 0;
                border: 1px solid $component_brd_color;
                border-radius: 5px;
                background: #ffffff;
                font-family: 'icomoon';
                font-size: 16px;
            }

            &:not([disabled]):hover {
                border-color: $component_brd_color_over;
                background: $component_bg_color_over;
            }

            &.is-active {
                border-color: $component_brd_color_active !important;
                background: $component_bg_color_active !important;
                color: #ffffff !important;
            }

            &[disabled],
            &.is-disabled {
                border: 1px solid $component_brd_color_disabled;
                background: $component_bg_color_disabled;
                color: $component_font_color_disabled;
                cursor: default;
                pointer-events: none;

                &:after {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                }
            }

            &.prev {
                margin-right: 8px;
            }
            &.next {
                margin-left: 8px;
            }

            @media ($mq_mobile) {
                // Mobile
                display: none;
            }
        }

        .is-mobile {
            display: none;
            float: left;
            padding: 0 12px;
            line-height: 30px;

            @media ($mq_mobile) {
                // Mobile
                @include inlineBlock();
            }
        }
    }
}

// Content BOX
.ui_con_box {
    padding: 0 20px 20px;
    background: #ffffff;
    box-sizing: border-box;

    > section {
        display: block;

        > h3 {
            padding: 13px 0 0 0;
            color: #333333;
            font-size: 1.167em;
            line-height: 30px;
        }

        > .wrap {
            position: relative;
        }
    }

    > .page_footer {
        padding: 20px 0;
        text-align: center;

        > .wrap {
            @include inlineBlock();
            @include clearBoth();

            > * {
                float: left;
                margin: 0 5px 0 0;

                &:last-child {
                    margin: 0;
                }
            }
        }
    }
}

.ui_row {
    position: relative;
    max-width: $doc_max_wid;
    margin: 0 auto;
    margin-bottom: 50px;

    > .wrap {
        display: flex;
        flex-wrap: nowrap;
        align-items: stretch;
        justify-content: space-between;
        position: relative;

        > * {
            flex: 1 1;
        }

        > .ui_col {
            position: relative;
            max-width: 100%;

            @for $Loop1 from 1 through 24 {
                &.w#{$Loop1} {
                    flex: 1 1 calc(4.1666% * #{$Loop1});
                    box-sizing: border-box;
                }
            }
        }

        > hr {
            flex: 0 0;
            height: auto;
            border: none;
            border-right: 1px dashed #e6e6e6;
            background: none;
            box-sizing: border-box;
        }

        &.is-cols-pad {
            gap: 20px;

            // @media ($mq_mobile) {
            //     // Mobile
            //     > .ui_col {
            //         padding-bottom: 30px;

            //         &:last-child {
            //             padding-bottom: 0;
            //         }
            //     }
            // }
        }
    }

    &.is-2dp {
        margin-bottom: 20px;
    }

    &.is-3dp {
        margin-bottom: 6px;

        > .wrap {
            gap: 6px;
        }
    }

    &:last-child {
        margin-bottom: 0;
    }

    @media ($mq_mobile) {
        // Mobile
        margin-bottom: 30px;
    }
}
.ui_box {
    > .content {
        > hr,
        > .wrap > hr {
            display: block;
            height: 1px;
            margin: 20px 0;
            border: none;
            border-bottom: 1px dashed #e6e6e6;
            background: none;
            box-sizing: border-box;
        }
    }
}
.ui_col_boxs {
    display: flex;
    align-items: stretch;
    gap: 20px;

    > .item {
        flex: 0 0;

        > .header {
            display: flex;
            height: 24px;
            padding-bottom: 6px;
            line-height: 24px;

            > .rc {
                margin-left: auto;
            }

            & + * {
                height: calc(100% - 30px);
            }
        }
    }

    > hr {
        flex: 0 0 !important;
        height: auto !important;
        margin: 0 !important;
        border: none !important;
        border-right: 1px dashed #e6e6e6 !important;
        background: none !important;
        box-sizing: border-box !important;
    }
}

.ui_grid {
    margin: -10px;
    @include clearBoth();

    > .item {
        @for $Loop1 from 1 through 24 {
            &.w#{$Loop1} {
                float: left;
                width: 4.1666% * $Loop1;
                padding: 10px;
                box-sizing: border-box;

                > .title {
                    display: block;
                    position: relative;
                    border-radius: 4px;
                    margin: 0 10px 10px;
                    background: rgba(0, 0, 0, 0.05);
                    line-height: 30px;
                    text-align: center;

                    &:after {
                        content: '';
                        position: absolute;
                        top: 30px;
                        left: 50%;
                        margin: 0 0 0 -11px;
                        @include triangle('bottom', rgba(0, 0, 0, 0.05), 22px, 10px);
                    }
                }
            }
        }

        @media ($mq_mobile) {
            // Mobile
            float: none !important;
            width: 100% !important;
            padding-left: 0 !important;
            padding-right: 0 !important;
        }
    }
}

.ui-loader-container {
    position: relative;

    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(255, 255, 255, 0.8);
        z-index: 0;
        visibility: hidden;
        opacity: 0;
        @include transition(0.18);
        z-index: 2;
    }
    &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 40px;
        height: 40px;
        margin: -20px 0 0 -20px;
        border-top: 4px solid #666;
        border-right: 4px solid rgba(0, 0, 0, 0.2);
        border-bottom: 4px solid rgba(0, 0, 0, 0.2);
        border-left: 4px solid rgba(0, 0, 0, 0.2);
        border-radius: 50%;
        visibility: hidden;
        opacity: 0;
        @include transition(0.18);
        z-index: 2;
    }

    &.is-loading {
        &:before,
        &:after {
            visibility: visible;
            opacity: 1;
        }
        &:after {
            animation: loaderAni 1.1s infinite cubic-bezier(0.5, 0, 0.5, 1);
        }
    }

    &.ui_box {
        &:before:not(.is-not-header) {
            top: 55px;
            height: calc(100% - 55px);
        }
    }
}

.ui_box {
    position: relative;
    padding: 12px 20px 20px;
    border-radius: 5px;
    background: #ffffff;
    box-sizing: border-box;
    @include shadow(0, 2px, 4px, 0.05, 0, 0, 0);

    // Loading
    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(255, 255, 255, 0.8);
        z-index: 0;
        visibility: hidden;
        opacity: 0;
        transition: visibility 0s linear 300ms, opacity 300ms, transform 300ms;
        z-index: 2;
    }
    &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 40px;
        height: 40px;
        margin: -20px 0 0 -20px;
        border-top: 4px solid #666;
        border-right: 4px solid rgba(0, 0, 0, 0.2);
        border-bottom: 4px solid rgba(0, 0, 0, 0.2);
        border-left: 4px solid rgba(0, 0, 0, 0.2);
        border-radius: 50%;
        visibility: hidden;
        opacity: 0;
        transition: visibility 0s linear 300ms, opacity 300ms;
        z-index: 2;
    }

    &.is-loading {
        &:before,
        &:after {
            visibility: visible;
            opacity: 1;
        }
        &:after {
            animation: loaderAni 1.1s infinite cubic-bezier(0.5, 0, 0.5, 1);
        }

        > .header {
            > h2,
            > h3,
            > h4,
            > h5,
            > h6 {
                z-index: 3;
            }
            > .ui_tab.is-box {
                z-index: 3;
            }
        }
    }

    // 헤더 영역
    > .header {
        position: relative;
        padding: 0 0 12px 0;

        &:after {
            content: '';
            position: absolute;
            bottom: 0;
            left: -20px;
            width: calc(100% + 40px);
            height: 1px;
            background: #eeeeee;
        }

        > .wrap {
            display: flex;
            justify-content: space-between;
            flex-wrap: nowrap;

            > h2,
            > h3,
            > h4,
            > h5,
            > h6 {
                display: flex;
                flex: 1 1 auto;
                min-width: 0;
                position: relative;
                line-height: 30px;

                > span,
                > strong {
                    display: flex;
                    flex: 0 1 auto;
                    align-items: center;
                    min-width: 0;
                    font-size: 18px;
                    margin: 0 8px 0 0;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;

                    > * {
                        flex: 0 1 auto;
                        max-width: 200px;
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;

                        &:not(:first-child) {
                            position: relative;
                            padding-left: 11px;
                            margin-left: 10px;

                            &:before {
                                content: '';
                                position: absolute;
                                top: 50%;
                                left: 0;
                                width: 1px;
                                height: 1em;
                                margin: -0.5em 0 0 0;
                                border-left: 2px dotted $component_brd_color;
                            }
                        }
                    }
                }

                @at-root .sub_title {
                    display: flex;
                    flex: 0 1 auto;
                    flex-wrap: nowrap;
                    min-width: 0;
                    color: $font_color;
                    font-size: 12px;
                    line-height: 22px;

                    > * {
                        flex: 0 1 auto;
                        align-self: center;
                        padding: 0 8px;
                        margin: 1px 4px 1px 0;
                        border: 1px solid $component_brd_color;
                        border-radius: 100px;
                        background: #fff;
                        font-weight: normal;
                        line-height: 20px;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        overflow: hidden;

                        > * {
                            // display: block;
                            overflow: hidden;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                        }

                        &.is-searchInput {
                            border-color: $search_keyword_color;
                            background: $search_keyword_color;
                            color: #ffffff;
                        }

                        &.is-customColor {
                            border-color: var(--color);
                            background-color: var(--color);
                            color: #ffffff;
                        }
                    }
                    > button {
                        position: relative;
                        cursor: pointer;
                        @include transition();
                        overflow: visible;

                        &:after {
                            content: '\e019';
                            position: absolute;
                            top: -8px;
                            right: -3px;
                            transform: scale(0.8);
                            width: 16px;
                            height: 16px;
                            border-radius: 50%;
                            background: $font_color_dm;
                            font-family: 'icomoon';
                            color: #fff;
                            font-size: 10px;
                            line-height: 16px;
                            text-align: center;
                            @include opacity(0);
                            @include transition();
                        }

                        &:hover {
                            &:after {
                                right: -6px;
                                @include opacity(1);
                            }
                        }
                    }
                }

                &:before {
                    content: '';
                    position: absolute;
                    top: 50%;
                    left: -30px;
                    width: 20px;
                    height: 7px;
                    margin: -3px 0 0 0;
                    border-radius: 4px;
                    background: $point_color;
                }

                @media ($mq_mobile) {
                    // Mobile
                    &:before {
                        left: -22px;
                        border-radius: 0;
                    }
                }
            }

            > .rc_wrap,
            > button.close {
                display: flex;
                // flex: 0 0 auto;
                flex-wrap: nowrap;
                padding-left: 30px;

                > * {
                    margin: 0 5px 0 0;

                    &:last-child {
                        margin-right: 0;
                    }
                }
            }
        }

        @media ($mq_mobile) {
            // Mobile
            &:after {
                left: -10px;
                width: calc(100% + 20px);
            }
        }
    }

    // 컨텐츠 영역
    > .content,
    > div > .content {
        position: relative;
        padding: 20px 0 0;
    }

    // 0 Depth 박스
    &.is-0dp {
        padding: 0;
        margin-top: 0;
        background: transparent;
        @include shadow(0, 0, 0, 0);

        > .header {
            padding: 0 0 12px 0;

            &:after {
                content: none;
            }

            > .wrap {
                > h3,
                > h4,
                > h5,
                > h6 {
                    padding-left: 20px;
                    line-height: 24px;

                    &:before {
                        left: -10px;
                        height: 12px;
                        margin: -6px 0 0 0;
                        border-radius: 100px;
                    }

                    > span {
                        font-size: 24px;
                    }
                }

                > .rc_wrap {
                    top: 0;
                    right: 0;
                    margin-top: 0;
                }
            }
        }

        > .ui_function,
        > div > .ui_function {
            padding: 8px;

            & + .content {
                padding: 10px 0 0 0;
            }
        }

        > .content {
            padding: 0;
        }

        @media ($mq_mobile) {
            // Mobile
            > .header {
                padding: 0 0 10px 0;
            }
        }
    }

    // 2 Depth 박스
    &.is-2dp {
        padding: 0;
        margin-top: 0;
        background: transparent;
        @include shadow(0, 0, 0, 0);

        > .header {
            padding: 0 0 12px 0;

            &:after {
                content: none;
            }

            > .wrap {
                > h3,
                > h4,
                > h5,
                > h6 {
                    line-height: 24px;

                    &:before {
                        content: none;
                    }

                    > span {
                        font-size: 16px;
                    }
                }

                > .rc_wrap {
                    top: 0;
                    right: 0;
                    margin-top: 0;
