// 정렬(오름/내림) 버튼
.ui_sort {
    display: inline-block;
    position: relative;
    padding: 0 18px 0 0;
    //cursor: pointer;

    .txt {
        display: block;
        line-height: 1.2em;
        z-index: 0;
    }
    input {
        @include uiComponentHidden();

        & + label {
            position: absolute;
            top: 50%;
            right: 0;
            width: 10px;
            height: 10px;
            border: 1px solid #a3a3a3;
            background: #ffffff;
            box-sizing: border-box;
            text-align: left;
            text-indent: -9999px;
            cursor: pointer;
            z-index: 2;

            /*
			&:before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: rgba(0,0,0,0.001);
			}
			*/
        }
        &:nth-child(4) + label {
            z-index: 1;
        }

        &:nth-child(2) + label {
            margin-top: -11px;

            &:after {
                content: '';
                position: absolute;
                top: 50%;
                left: 50%;
                width: 0px;
                height: 0px;
                margin: -2px 0 0 -3px;
                border-left: 3px solid transparent;
                border-right: 3px solid transparent;
                border-bottom: 4px solid #999999;
                @include transition();
            }
        }
        &:nth-child(4) + label {
            margin-top: 1px;

            &:after {
                content: '';
                position: absolute;
                top: 50%;
                left: 50%;
                width: 0px;
                height: 0px;
                margin: -2px 0 0 -3px;
                border-left: 3px solid transparent;
                border-right: 3px solid transparent;
                border-top: 4px solid #999999;
                @include transition();
            }
        }

        &:hover {
            & + label {
                border: 1px solid #666666;
            }

            &:nth-child(2) + label:after {
                border-bottom-color: #666666;
            }

            &:nth-child(4) + label:after {
                border-top-color: #666666;
            }
        }

        &:checked {
            & + label {
                border: 1px solid #666666;
                background: #666666;
                z-index: 1;
            }

            &:nth-child(2) + label:after {
                border-bottom-color: #ffffff;
            }

            &:nth-child(4) + label:after {
                border-top-color: #ffffff;
            }
        }
    }
}

