            @extend .ui_btn;
            padding: 6px !important;
            font-family: 'icomoon';
            font-size: 16px;
        }

        &:checked {
            & + label {
                @extend .ui_btn, .is-active;
            }
        }
    }

    &.is-small {
        > input {
            & + label {
                height: 24px !important;
                padding: 3px 4px !important;
                font-size: 14px;
            }
        }
    }
}
.ui_help_box {
    width: 100%;
    height: 0;
    overflow: hidden;

    > .wrap {
        padding: 14px 0 0;

        > ul {
            display: block;
            position: relative;
            padding: 15px 20px 15px;
            border-radius: 5px;
            background: rgba(0, 0, 0, 0.05);
            box-sizing: border-box;

            &:before {
                content: '';
                position: absolute;
                top: -12px;
                right: 42px;
                @include triangle('top', rgba(0, 0, 0, 0.05), 16px, 12px);
            }

            > li {
                padding: 0 0 8px 0;

                &:last-child {
                    padding: 0;
                }

                > strong,
                > span {
                    display: block;
                    position: relative;
                    padding: 0 0 0 12px;
                    line-height: 16px;

                    &:before {
                        content: '';
                        position: absolute;
                        top: 6px;
                        left: 0;
                        width: 3px;
                        height: 3px;
                        background: #666666;
                    }
                }

                > ul {
                    display: block;
                    padding: 3px 0 0 12px;

                    > li {
                        position: relative;
                        padding: 0 0 3px 12px;
                        font-size: 0.917em;
                        line-height: 16px;

                        &:last-child {
                            padding-bottom: 0;
                        }

                        &:before {
                            content: '';
                            position: absolute;
                            top: 8px;
                            left: 2px;
                            width: 4px;
                            height: 1px;
                            margin: -1px 0 0 0;
                            background: #999999;
                        }
                    }

                    &.ex {
                        > li {
                            padding-left: 32px;
                            color: #999999;

                            &:before {
                                content: '[ex] -';
                                position: absolute;
                                top: 1px;
                                left: 2px;
                                width: auto;
                                height: auto;
                                margin: -1px 0 0 0;
                                background: none;
                                font-weight: bold;
                            }
                        }
                    }
                }

                em {
                    color: #999999;
                }
            }
        }
    }

    &.r_0 {
        > .wrap {
            > ul {
                &:before {
                    right: 8px;
                }
            }
        }
    }
    &.r_2 {
        > .wrap {
            > ul {
                &:before {
                    right: 77px;
                }
            }
        }
    }
    &.r_3 {
        > .wrap {
            > ul {
                &:before {
                    right: 112px;
                }
            }
        }
    }

    &.is-fixed {
        height: auto;

        > .wrap {
            padding: 0;

            > ul {
                &:before {
                    content: none;
                }
            }
        }
    }
}

// Image
.ui_image {
    position: relative;
    width: 50px;
    height: 50px;
    background: #e1e1e1;
    text-align: center;
    overflow: hidden;

    &.is-no-image {
        > img {
            @include opacity(0);
        }

        &:after {
            content: '\e048';
            position: absolute;
            top: 50%;
            left: 0;
            width: 100%;
            margin: -12px 0 0 0;
            font-family: 'icomoon';
            color: #cccccc;
            font-size: 24px;
            line-height: 24px;
        }
    }

    .background {
        position: absolute;
        top: -5%;
        left: -5%;
        width: 110%;
        height: 110%;
        background-size: cover;
        @include blur(10);
        @include opacity(0.6);
    }

    &.is-auto {
        height: 100%;

        &.is-no-image {
            &:after {
                line-height: 1em;
            }
        }
    }
    &.is-60 {
        width: 60px;
        height: 60px;
        line-height: 60px;

        &:after {
            font-size: 24px;
        }
    }
    &.is-50 {
        width: 50px;
        height: 50px;
        line-height: 50px;

        &:after {
            font-size: 24px;
        }
    }
    &.is-40 {
        width: 40px;
        height: 40px;
        line-height: 40px;

        &:after {
            font-size: 24px;
        }
    }

    > img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        max-width: 100%;
        max-height: 100%;
        vertical-align: middle;
    }
}

// Rank Variance
.ui_rank_variance {
    > ul {
        white-space: normal;

        > li {
            // float: left;
            @include inlineBlock();
            position: relative;
            padding: 2px 0 2px 20px;

            &:first-child {
                padding-left: 0;

                &:before {
                    content: none !important;
                }
            }

            &:before {
                content: '\e051';
                position: absolute;
                top: 2px;
                left: 0;
                width: 20px;
                font-family: 'icomoon';
                color: #d9d9d9;
            }

            .item {
                padding: 4px 6px;
                border-radius: 100px;
                background: #e8e8e8;
            }
        }

        @media ($mq_mobile) {
            // Mobile
            white-space: nowrap;
        }
    }
}
