@charset "utf-8";



#lnb {
	position: relative;
	z-index: 20;

	/*
	&.fixed {
		position: fixed !important;
		top: 40px;
	}
	*/

	nav {
		> ul > li {
			position: relative;
			padding: 0 0 1px 0;

			&.active {
				border-left: 4px solid #2255b4;
			}
			> div {
				> a {
					display: block;
					position: relative;
					padding: 15px 35px 16px 20px;
					background: #ffffff;
					overflow: hidden;
					color: #666666;
					font-size: 1.333em;
					font-weight: bold;
					line-height: 1.2em;
					@include transition();

					&:before {
						content: '';
						position: absolute;
						top: 0;
						left: -48px;
						width: 92px;
						height: 92px;
						background: url(~static/img/ui/lnb_img.png) no-repeat 0 0;
						opacity: 0;
						z-index: 0;
						@include rotate( -50 );
						@include transition();
					}

					.txt {
						position: relative;
						z-index: 1;
					}

					&.active {
						background: $point_color !important;
						color: #ffffff;

						&:before {
							opacity: 1;
							@include rotate( 0 );
							@include transition( 0.3 );
						}
					}

					&.expanded {
						background: #c2c2c2;
						color: #ffffff;

						&:before {
							opacity: 1;
							@include rotate( 0 );
							@include transition( 0.3 );
						}
					}


					&.has_sub {
						.icon {
							position: absolute;
							top: 50%;
							right: 20px;
							width: 14px;
							height: 14px;
							margin: -7px 0 0 0;
							@include rotate( 0 );
							@include transition();

							&:before {
								content: '';
								position: absolute;
								top: 50%;
								left: 0;
								width: 100%;
								height: 2px;
								margin: -1px 0 0 0;
								background: #666666;
								@include transition();
							}
							&:after {
								content: '';
								position: absolute;
								top: 0;
								left: 50%;
								width: 2px;
								height: 100%;
								margin: 0 0 0 -1px;
								background: #666666;
								@include transition();
							}
						}
						&.expanded, &.active, &:hover {
							.icon {
								&:before, &:after {
									background: #ffffff;
								}
							}
						}
						&.expanded {
							.icon {
								@include rotate( 180 );

								&:after {
									top: 50%;
									height: 0;
								}
								//@include rotate( 225 );
							}
						}
					}
				}
				> .sub {
					height: 0;
					background: #ffffff;
					overflow: hidden;

					> ul {
						> li {
							> a {
								display: block;
								position: relative;
								padding: 11px 20px 11px 35px;
								border-left: 2px solid #c2c2c2;
								color: #666666;
								font-size: 1.167em;
								font-weight: bold;
								line-height: 18px;
								@include transition();

								> span {
									@include inlineBlock();
									position: relative;
									padding: 1px 0;
								}
								&:before {
									content: '';
									position: absolute;
									top: 50%;
									left: 20px;
									width: 0px;
									height: 0px;
									border-top: 3px solid transparent;
									border-bottom: 3px solid transparent;
									border-left: 4px solid #666666;
									margin: -2px 0 0 0;
									@include transition();
								}
								&:hover {
									border-left: 2px solid #666666;
									background: #e3e3e3;
									color: #333333;
								}
								&.active {
									border-left: 2px solid darken( $point_color, 20 ) !important;
									background: change_color( $point_color, $lightness: 92%, $saturation: 40% );
									color: darken( $point_color, 30 );

									&:before {
										border-left-color: darken( $point_color, 20 ) !important
									}
								}
							}
						}
					}
				}

				&:hover {
					> a {
						background: #c2c2c2;
						color: #ffffff;

						&:before {
							opacity: 1;
							@include rotate( 0 );
							@include transition( 0.3 );
						}
					}
				}

				a.active + .sub {
					> ul {
						> li {
							> a {
								border-left: 2px solid $point_color;

								&:hover {
									border-left: 2px solid darken( $point_color, 30 );
									background: change_color( $point_color, $lightness: 92%, $saturation: 40% );
									color: darken( $point_color, 30 );
								}
							}
						}
					}
				}
			}
		}
	}
}
