.ui_chart_container {
    position: relative;

    > .opts_wrap {
        display: flex;
        justify-content: flex-end;
        margin: 0 0 10px !important;

        > * {
            margin-left: 5px;
        }
    }
}

.ui_chart_wrap {
    position: relative;
    min-height: 100px;
    z-index: 1;

    * {
        font-family: inherit !important;
    }

    .amcharts-chart-div {
        width: 100% !important;
    }

    .amcharts-legend {
        *[aria-checked='false'] {
            .bg {
                fill: #d9d9d9;
                fill-opacity: 1;
            }
            .marker {
                @include opacity(0);
            }

            .amcharts-Circle-group {
                @include opacity(1);
            }

            .amcharts-Label-group:nth-child(2) {
                @include opacity(1);
            }

            .amcharts-Label-group:nth-child(4) {
                fill: #afafaf;
            }
        }
    }
    .amChartsLegend {
        circle,
        text {
            pointer-events: none;
        }

        // .amcharts-legend-value {
        //     font-weight: bold;
        // }
    }

    &.is-line,
    &.column-cluster,
    &.column-stack,
    &.column-stack-100 {
        .amcharts-balloon-bg.amcharts-balloon-bg-categoryAxis {
            transform: translateY(10px);
        }
        .amcharts-balloon-div.amcharts-balloon-div-categoryAxis {
            margin-top: 10px;
            // background: #888888;
        }
    }

    // Loading
    &.is-loader {
        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(255, 255, 255, 0.8);
            z-index: 0;
            visibility: hidden;
            opacity: 0;
            transition: visibility 0s linear 300ms, opacity 300ms, transform 300ms;
            z-index: 2;
        }
        &:after {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            width: 40px;
            height: 40px;
            margin: -20px 0 0 -20px;
            border-top: 4px solid #666;
            border-right: 4px solid rgba(0, 0, 0, 0.2);
            border-bottom: 4px solid rgba(0, 0, 0, 0.2);
            border-left: 4px solid rgba(0, 0, 0, 0.2);
            border-radius: 50%;
            visibility: hidden;
            opacity: 0;
            transition: visibility 0s linear 300ms, opacity 300ms;
            z-index: 2;
        }
        &.is-loading {
            &:before,
            &:after {
                visibility: visible;
                opacity: 1;
            }
            &:after {
                animation: loaderAni 1.1s infinite cubic-bezier(0.5, 0, 0.5, 1);
            }

            > .header {
                > h3,
                > h4,
                > h5,
                > h6 {
                    z-index: 3;
                }
                > .ui_tab.is-box {
                    z-index: 3;
                }
            }

            svg {
                visibility: hidden;
            }
        }
    }

    &.is-click {
        .amcharts-pie-slice {
            cursor: pointer;
        }
    }
}
.ui_chart_legend {
    text-align: center;

    &.is-pad-top {
        padding-top: 15px;
    }
    &.is-pad-bottom {
        padding-bottom: 15px;
    }

    ul {
        @include inlineBlock();
        @include clearBoth();

        li {
            float: left;
            padding: 2px 10px 2px 0;

            .item {
                display: block;
                position: relative;
                padding: 0 10px 0 24px;
                border-radius: 11px;
                overflow: hidden;
                color: $font_color;
                line-height: 22px;

                .bg {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    @include opacity(0.14);
                }

                .icon {
                    position: absolute;
                    top: 50%;
                    left: 6px;
                    width: 14px;
                    height: 14px;
                    margin: -7px 0 0 0;
                    border-radius: 7px;
                }

                .txt {
                    display: block;
                }
            }
        }
    }
}
.ui_word_cloud_container {
    position: relative;

    > .view_type {
        display: flex;
        justify-content: flex-end;
        padding-bottom: 6px;
    }

    .color_set {
        // width: 80%;
        margin: 10px 20px 0;
        overflow: hidden;
        table-layout: fixed;

        > ul {
            display: flex;
            justify-content: center;

            > li {
                flex: 1 1 0;
                max-width: 120px;
                padding: 0 5px;
                background: #aaaaaa;
                box-sizing: border-box;
                color: #ffffff;
                font-size: 10px;
                text-align: right;
                line-height: 1.4em;
                overflow: hidden;

                &:first-child {
                    border-top-left-radius: 10px !important;
                    border-bottom-left-radius: 10px !important;
                }
                &:last-child {
                    border-top-right-radius: 10px !important;
                    border-bottom-right-radius: 10px !important;
                }
            }
        }

        @media ($mq_mobile) {
            // Mobile
            margin: 0;

            > ul {
                > li {
                    padding: 0 2px;
                    font-size: 11px;

                    > span {
                        display: none;
                    }
                }
            }
        }
    }

    > .ui_btn {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 1;

        @media ($mq_mobile) {
            // Mobile
            display: none;
        }
    }
    > .expanded_view {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.65);
        box-sizing: border-box;
        z-index: 10;

        > .wrap {
            position: absolute;
            top: 50px;
            left: 50px;
            width: calc(100% - 100px);
            height: calc(100% - 100px);
            padding: 50px 0 0 300px;
            border-radius: 5px;
            background: #ffffff;
            box-sizing: border-box;

            > .close {
                display: block;
                position: absolute;
                top: 18px;
                right: 15px;
                width: 32px;
                height: 32px;
                text-align: left;
                text-indent: -9999px;
                z-index: 1;

                &:before {
                    content: '';
                    position: absolute;
                    top: 5px;
                    left: 50%;
                    width: 2px;
                    height: 22px;
                    margin: 0 0 0 -1px;
                    background: #666666;
                    @include rotate(45);
                    @include transition(0.3, 0);
                }
                &:after {
                    content: '';
                    position: absolute;
                    top: 50%;
                    left: 5px;
                    width: 22px;
                    height: 2px;
                    margin: -1px 0 0 0;
                    background: #666666;
                    @include rotate(45);
                    @include transition(0.3, 0.03);
                }

                &:hover {
                    &:before,
                    &:after {
                        @include rotate(225);
                    }
                }
            }

            .bg_top {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 7px;
                border-radius: 5px 5px 0 0;
                background: linear-gradient(135deg, $gr_color_01 0%, $gr_color_02 100%);
                filter: progid:DXImageTransform.Microsoft.gradient( startColorstr=#{$gr_color_01}, endColorstr=#{$gr_color_02}, GradientType=1 );
            }

            > .set {
                position: absolute;
                top: 7px;
                left: 0;
                width: 300px;
                height: calc(100% - 7px);
                padding: 15px;
                border-radius: 0 0 0 5px;
                background: #f9f9f9f9;
                box-sizing: border-box;

                > h3 {
                    padding: 0 0 15px 0;
                    color: $font_color_hl;
                    font-size: 1.333em;
                }

                > .sets {
                    > dl {
                        padding-bottom: 15px;

                        dt {
                            display: block;
                            padding: 0 0 5px 0;
                            color: $font_color_dm;
                        }

                        dd {
                            line-height: 30px;
                            @include clearBoth();

                            > * {
                                float: left;
                                margin-right: 4px;

                                &:last-child {
                                    margin-right: 0;
                                }
                            }
                        }
                    }
                }
            }
            > .view {
                position: relative;
                height: 100%;
                padding: 0 15px 15px;
                border-radius: 0 0 5px 0;
                background: #ffffff;
                box-sizing: border-box;

                .total {
                    padding-bottom: 5px;
                    color: $font_color_dm;
                    font-size: 1.167em;

                    strong {
                        color: $font_color;
                    }
                }

                .chart_wrap {
                    width: calc(100% - 2px);
                    height: calc(100% - 23px);

                    .resize_wrap {
                        position: relative;
                        left: 0 !important;
                        max-height: 100%;
                        border: 1px dashed #d6d6d6;
                        //overflow: hidden;

                        .size {
                            position: absolute;
                            top: 10px;
                            left: 10px;
                            height: 30px;
                            padding: 0 20px;
                            border-radius: 15px;
                            background: #f6f6f6;
                            line-height: 30px;

                            .separator {
                                color: $font_color_dm;
                            }
                        }
                    }

                    .ui-icon-gripsmall-diagonal-se {
                        cursor: se-resize;
                        position: absolute;
                        bottom: -10px;
                        right: -10px;
                        height: 30px;
                        width: 30px;
                        border: 1px solid #d4d4d4;
                        border-radius: 5px;
                        background: #ffffff;
                        box-sizing: border-box;
                        color: $font_color;
                        z-index: 1 !important;

                        &:before {
                            content: '\e049';
                            display: block;
                            font-family: 'icomoon';
                            line-height: 28px;
                            text-align: center;
                        }

                        &:hover {
                            border: 1px solid $component_brd_color_over;
                            background: $component_bg_color_over;
                            color: $font_color_hl;
                        }

                        &:not([disabled]):active,
                        &:not([disabled]).is-active {
                            border-color: $component_brd_color_active;
                            background: $component_bg_color_active;
                            color: #ffffff !important;
                        }

                        @at-root .ui-resizable-helper {
                            border: 2px dashed #585858;
                        }
                    }
                }

                // Loading
                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background: rgba(255, 255, 255, 0.8);
                    z-index: 0;
                    visibility: hidden;
                    opacity: 0;
                    transition: visibility 0s linear 300ms, opacity 300ms, transform 300ms;
                    z-index: 2;
                }
                &:after {
                    content: '';
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    width: 40px;
                    height: 40px;
                    margin: -20px 0 0 -20px;
                    border-top: 4px solid #666;
                    border-right: 4px solid rgba(0, 0, 0, 0.2);
                    border-bottom: 4px solid rgba(0, 0, 0, 0.2);
                    border-left: 4px solid rgba(0, 0, 0, 0.2);
                    border-radius: 50%;
                    visibility: hidden;
                    opacity: 0;
                    transition: visibility 0s linear 300ms, opacity 300ms;
                    z-index: 2;
                }

                &.is-loading {
                    &:before,
                    &:after {
                        visibility: visible;
                        opacity: 1;
                    }
                    &:after {
                        animation: loaderAni 1.1s infinite cubic-bezier(0.5, 0, 0.5, 1);
                    }

                    > .header {
                        > h3,
                        > h4,
                        > h5,
                        > h6 {
                            z-index: 3;
                        }
                        > .ui_tab.is-box {
                            z-index: 3;
                        }
                    }
                }
            }
        }

        @media ($mq_mobile) {
            // Mobile
            display: none;
        }
    }

    .render_txt {
        position: absolute;
        top: 50%;
        left: 0;
        width: 100%;
        text-align: center;
        @include opacity(0);

        &:before {
            content: '';
            position: absolute;
            top: -15px;
            left: 50%;
            width: 20px;
            height: 20px;
            margin: -10px 0 0 -10px;
            border-top: 2px solid #666;
            border-right: 2px solid rgba(0, 0, 0, 0.2);
            border-bottom: 2px solid rgba(0, 0, 0, 0.2);
            border-left: 2px solid rgba(0, 0, 0, 0.2);
            border-radius: 50%;
            animation: loaderAni 1.1s infinite cubic-bezier(0.5, 0, 0.5, 1);
        }
    }

    .ui_chart_wrap.is-wordCloud-v3 {
        box-sizing: border-box;
        overflow: hidden;

        .word_item {
            display: block !important;
            text-align: center;
            user-select: all !important;

            > * {
                pointer-events: none;
            }

            > .bg {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translateX(-50%) translateY(-50%);
                width: 100%;
                height: 100%;
                padding: 5% 8%;
                border: 2px solid #a1a1a1;
                border-radius: 200px;
                background: #a1a1a1;
                font-family: inherit !important;
                @include opacity(0);
            }

            > span:not(.bg) {
                display: block;
                position: relative;
                user-select: none;
            }

            &:hover,
            &.is-active {
                .bg {
                    @include opacity(1);
                }
                > span:not(.bg) {
                    color: #ffffff !important;
                }

                &.is-custom-active {
                    .bg {
                        border-color: var(--brdColor) !important;
                        background: var(--color) !important;
                    }
                }
            }
        }

        &.is-click {
            .word_item {
                cursor: pointer;
            }
        }

        &.ui_nodata {
            > * {
                display: none !important;
            }
        }
    }

    &.is-click {
        .word_item {
            cursor: pointer;
        }
    }

    .bubble_wrap {
        display: block;
        float: left;
        height: 0;
        zoom: 1;
        pointer-events: none;

        .ui_bubble_box {
            position: fixed;
            color: #ffffff;
            z-index: 3;

            &:before {
                content: '';
                position: absolute;
                top: -2px;
                left: -2px;
                width: 100%;
                height: 100%;
                border: 2px solid #ffffff;
                @include shadow(0, 3px, 6px, 0.2, 0, 0, 0);
                z-index: 1;
            }

            &:after {
                content: '';
                position: absolute;
                bottom: -10px;
                left: 50%;
                margin: 0 0 0 -9px;
                @include triangle('bottom', #ffffff, 18px, 12px);
                z-index: 1;
            }

            .arrow {
                position: absolute;
                bottom: -8px;
                left: 50%;
                margin: 0 0 0 -7px;
                @include triangle('bottom', #666666, 14px, 10px);
                text-align: left;
                text-indent: -9999px;
                z-index: 2;
            }

            &.is-active {
                margin-top: 0;
                @include opacity(1);
            }

            .wrap {
                position: relative;
                padding: 15px 30px;
                background: #666666;
                z-index: 2;

                .infos {
                    display: block;
                    text-align: center;

                    .attr {
                        display: block;
                        padding-bottom: 5px;
                        white-space: nowrap;
                    }

                    .title {
                        display: block;
                        font-size: 1.333em;
                        font-weight: bold;
                        line-height: 1;
                        white-space: nowrap;
                    }

                    .dv {
                        display: block;
                        padding: 5px 0 2px;
                        font-size: 2.167em;
                        line-height: 1;
                        white-space: nowrap;
                    }

                    .ui_fluc {
                        min-width: 30px;
                        margin: 2px 0 4px;
                    }

                    .per {
                        display: block;
                        padding: 0;
                        font-size: 1.167em;
                        line-height: 1;
                        white-space: nowrap;
                    }
                }
            }

            &.is-black {
                color: #000000;
            }
        }
    }

    &.is-legend {
        padding-bottom: 40px;
    }
}

// 차트 툴팁
.chart_tooltip {
    display: table;
    padding: 20px 10px;
    text-align: center;

    .per {
        display: table-cell;
        padding: 0 10px 0 0;
        color: #ffffff;
        font-size: 2.667em;
        line-height: 1;
        vertical-align: middle;
        pointer-events: none;
    }

    .other {
        display: table-cell;

        .title {
            display: block;
            position: relative;
            padding: 0 0 6px 0;
            color: #ffffff;
            font-size: 1.167em;
            line-height: 1;

            &:after {
                content: '';
                position: absolute;
                left: 0;
                bottom: 0;
                width: 100%;
                height: 1px;
                background: rgba(255, 255, 255, 0.3);
            }
        }

        .dv {
            display: block;
            padding: 5px 0 0 0;
            color: #ffffff;
            line-height: 1;
        }
    }
}

.chart_tooltip2 {
    display: table;
    padding: 0;
    text-align: center;

    > * {
        display: table-cell;
        position: relative;
        padding: 0 11px 0 10px;

        &:not(:last-child) {
            &:after {
                content: '';
                position: absolute;
                right: 0;
                top: 50%;
                width: 1px;
                height: 14px;
                margin: -7px 0 0 0;
                background: rgba(255, 255, 255, 0.3);
            }
        }

        &:first-child {
            font-size: 1.667em;
        }
    }

    .per {
        color: #ffffff;
        line-height: 1;
        vertical-align: middle;
    }

    .title {
        color: #ffffff;
        line-height: 1.667em;
    }

    .dv {
        color: #ffffff;
        line-height: 1.667em;
    }
}

.v3_chart_tooltip {
    display: block;
    padding: 15px 25px;
    color: #ffffff;
    text-align: center;

    .title {
        display: block;
        font-size: 16px;
        font-weight: normal;
        line-height: 1;
    }
    .dv {
        display: block;
        padding: 5px 0 3px 0;
        font-size: 26px;
        font-weight: bold;
        line-height: 1;
    }
    .per {
        display: block;
        padding: 2px 0 0 0;
        font-size: 14px;
        line-height: 1;
    }

    &.is-wide {
        padding: 15px 30px;
    }
    &.is-black {
        color: #000000;
    }
}

.v3_chart_tooltip2 {
    display: block;
    padding: 3px 5px;
    color: #ffffff;
    text-align: center;

    .title {
        @include inlineBlock();
        font-weight: normal;
        line-height: 20px;
        vertical-align: top;
    }
    .dv {
        @include inlineBlock();
        padding: 0 2px 0 5px;
        color: inherit;
        font-size: 16px;
        font-weight: bold;
        line-height: 20px;
        vertical-align: top;
    }
    .per {
        @include inlineBlock();
        line-height: 20px;
        vertical-align: top;
    }

    &.is-wide {
        padding: 10px 20px;
    }
    &.is-black {
        color: #000000;
    }
}

// 나이별 차트
.ui_chart_wrap {
    &.is-age {
        display: flex;
        flex-wrap: nowrap;
        justify-content: center;
        gap: 0;
        text-align: center;

        > .item {
            flex: 0 1 45px;
            min-width: 20px;
            display: flex;
            flex-direction: column;

            .gage {
                flex: 1 0;
                display: flex;
                flex-direction: column-reverse;
                min-width: 12px;
                max-width: 30px;
                padding-top: 3em;
                margin: 0 auto;
                // border: 1px solid #ebebeb;
                border-radius: 100px 100px 0 0;
                background: #f4f4f4;
                @include transition();

                .ui_bubble {
                    flex: 1 0;
                    display: flex;
                    flex-direction: column-reverse;
                }

                .bar {
                    position: relative;
                    width: 100%;
                    bottom: 0;
                    border-radius: 100px 100px 0 0;

                    > .ageChartInfo {
                        position: absolute;
                        padding: 0 5px 0 3px;
                        white-space: nowrap;
                        text-align: left;
                        display: none;
                        left: 15px;
                        top: -13px;
                        z-index: 1;
                        opacity: 0.9;
                        border: 4px solid red;
                        background: #ffff;
                    }

                    &:before {
                        content: attr(data-content);
                        position: absolute;
                        top: -18px;
                        left: 0;
                        width: 100%;
                        height: 0;
                    }
                }
            }

            .category {
                flex: 0 0 24px;
                padding: 12px 0 0;
                line-height: 1;
                white-space: normal;
                text-align: center;
            }
        }

        .bubble_wrap {
            display: block;
            height: 0;
            pointer-events: none;
        }
    }
}

// 감성 게이지 차트
.ui_senti_gage {
    padding: 0;
    height: 16px;

    > ul {
        display: flex;
        height: 100%;
        border-radius: 200px;
        overflow: hidden;

        > li {
            flex-grow: 1;
            color: #ffffff;
            font-size: 11px;

            > * {
                display: block;
                height: 100%;
            }

            .gage_item {
                display: flex;
                justify-content: center;
                align-items: center;
                height: 100%;
                text-align: center;
            }
        }
    }
}
