@charset "utf-8";

#excels {
    // display: none;
    position: fixed;
    right: 10px;
    bottom: 20px;
    z-index: 100;

    > .badge {
        position: absolute;
        transform: translateX(-50%) translateY(-50%);
        padding: 5px 8px;
        // border: 1px solid #ff0000;
        border-radius: 100px;
        background: #ff0000;
        color: #fff;
        text-align: center;
        line-height: 1;
    }

    > .input {
        input {
            @include customComponent();

            & + label {
                display: block;
                width: 60px;
                height: 40px;
                border: 1px solid #d4d4d4;
                border-radius: 4px;
                background: #fff;
                text-align: center;
                line-height: 40px;
            }
        }
    }

    > .list {
        position: absolute;
        right: 70px;
        bottom: 0;
        width: 500px;
        height: 250px;
        padding: 10px;
        border: 1px solid #d4d4d4;
        border-radius: 4px;
        background: #ffff;
        box-sizing: border-box;
        // @include opacity(0);
        @include transition(0.24);

        .header {
            position: relative;
            top: 0;
            left: 0;
            padding: 12px 10px;
            margin: -10px;
            color: #fff;
            background-color: #6a6a6a;

            .close_btn {
                position: absolute;
                top: 13px;
                right: 10px;
            }
        }

        .content {
            position: relative;
            text-align: center;
            margin-top: 20px;
            height: 190px;

            .list_header {
                display: flex;
                padding: 8px 0;
                border-bottom: 1px solid #d4d4d4;

                .path {
                    width: 50%;
                }
                .date {
                    width: 30%;
                }
                .progress {
                    width: 10%;
                }
                .delete {
                    width: 10%;
                }
            }

            .excel_list {
                overflow-y: auto;
                height: 150px;

                &::-webkit-scrollbar {
                    display: none;
                }
            }

            .excel_item {
                display: flex;
                padding: 10px 0;

                &:nth-child(even) {
                    background: #f8f8f8;
                }

                .path {
                    width: 50%;
                }
                .date {
                    width: 30%;
                }
                .progress {
                    width: 10%;
                }
                .delete {
                    width: 10%;

                    &:disabled {
                        cursor: not-allowed;
                    }
                }
            }

            .empty {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }
}
